<template>
  <div class="center">
    <div class="right_main">
      <div class="right_main_tab">
        <div class="alarm_set_tab">
          <!-- <to-input 
            @sInput="getChildCon"
            input-size="选择日期" 
            input-placeholder="请选择日期"
          ></to-input> -->
          <ul class="time">
            <li 
              :class="activeClass == index ? 'active':''" 
              v-for="(itme,index) in itmeList" 
              :key="index" 
              @click="getItme(index)"
            >{{itme}}</li>
          </ul>
        </div>
        <div class="right_main_tab1">
          <div class="right_main_tab1_top">
            <h2>历史客流量</h2>
            <p>
              <a 
                :class="activeClass1 == index ? 'active':''" 
                v-for="(itme,index) in itmeList1" 
                :key="index" 
                @click="getItme1(index)"
                :title="itme"
              ></a>
            </p>
          </div>
          <div v-if="filteredUsers" class="tab_overflow" :style="'max-height:'+ Height">
            <table class="da">
              <thead>
                <tr>
                  <th>{{this.aKey}}客流量最高峰值</th>
                  <th>{{this.aKey}}客流量最低峰值</th>
                  <th>{{this.aKey}}平均客流量</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{{ this.gaoData | sum }}</td>
                   <td>{{ this.diData | sum }}</td>
                  <td>{{ this.averageData | sum }}</td>
                </tr>
              </tbody>
            </table>

            <div v-show="showPrise" id="myChart1" :style="{ height: !filteredUsers.length ? '0px' : '400px' }"></div>
            <div v-show="showRentPrise" 
                style="display:none;" 
                id="index_bottom1_pie_bj"
            ></div>
            <table>
              <thead>
                <tr>
                  <th>日期</th>
                  <th>游客人数</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(user, index) in filteredUsers" :key="index">
                  <td>{{ user.areaname }}</td>
                  <td>{{ user.kpivalue | sum }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="!filteredUsers.length" style="margin:10px auto;color:#f00;text-align:center;">暂无数据</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import echarts from 'echarts'
import ToInput from '../components/ToInput'
export default {
  name: 'vipSource',
  components: {
    ToInput
  },
  props: {
    listData:{},
    routerHeight: ''
  },
  data () {
    return {
      pig_data:[],
      gaoData:0,
      diData:0,
      averageData:0,
      zData:0,
      showPrise:true,
      showRentPrise:false,
      itmeList1:['柱图统计','饼图统计'],
      itmeList:['日','周','月','季','年'],
      activeClass: 0,
      activeClass1: 0,
      Height: parseInt(this.routerHeight) - 170 +'px',
      sData:[],
      xData:[],
      progress: [],
      fKey: '',             // 过滤 name 的关键字
      areaname: '',          // 上一次过滤的 name 关键字，初始化为''
      aKey: '日',           // audit 的关键字
      name: '',             // 上一次过滤的 audit关键字，初始化为''
    }
  },
  watch: {
    progress: {
      handler () {
        this.progress
      },
      deep: true
    }
  },
  // 计算属性computed
  computed: {
    filteredUsers () {
      let fUsers = this.queryFilter('areaname', this.fKey, this.progress)
          fUsers = this.queryFilter('name', this.aKey, fUsers)
      //let fUsers = this.queryFilter('name', this.aKey, this.progress)
      this.xData = [];
      this.sData = [];
      this.zData = this.averageData = 0;
      this.pig_data=[];
      for(let i=0;i<fUsers.length;i++){
        this.zData+=Number(fUsers[i].kpivalue);
        this.averageData = Math.ceil(this.zData / fUsers.length)
        this.xData.push( fUsers[i].areaname )
        this.sData.push( Number(fUsers[i].kpivalue) )
        this.pig_data.push({value:Number(fUsers[i].kpivalue), name:fUsers[i].areaname})
      }
      this.gaoData = (Math.max.apply(Math,this.sData))
      this.diData = (Math.min.apply(Math,this.sData))
      setTimeout(()=>{
        this.drawLine()
        this.drawLine1()
      }, 100)
      return fUsers;
    }
  },
  methods: {
    drawLine1(){
      let myChart = echarts.init(document.getElementById('index_bottom1_pie_bj'))
      myChart.setOption({
        tooltip : {
          backgroundColor : '#19486e',
          borderColor : '#228f96',
          borderRadius : 10,
          borderWidth: 1,
          textStyle:{
            fontSize:14,
            align:'left'
          },
          trigger: 'item',
          formatter: '{b}<br/> {a}:{c}人 <br/>占比:{d} %'
        },
        series : [
            {
                name:'游客人数',
                type:'pie',
                radius : '90%',
                center: ['50%', '50%'],
                data: this.pig_data,
                roseType: 'radius',
                labelLine: {
                  normal: {
                      lineStyle: {
                          color: 'rgba(255, 255, 255, 1)'
                      }
                  }
                },
                label: {
                  normal: {
                      fontFamily:"Arial",
                      fontSize:14,
                      formatter: "{b}\n {c}人",
                      color: '#fff'
                  }
                }
            }
        ],
        noDataLoadingOption:{
          text: '暂无数据',
          effect: 'dynamicLine'
        }
      });
    },
    drawLine () {
      let myChart = echarts.init(document.getElementById('myChart1'))
      myChart.setOption({
        tooltip : {
          backgroundColor : '#19486e',
          borderColor : '#228f96',
          borderRadius :10,
          borderWidth:1,
          textStyle:{
            align:'shadow'
          },
          trigger: 'item',
          formatter: '{b} <br/>{a} : {c}'
        },
        grid: {
          left: '2%',
          right: '2%',
          bottom: '2%',
          top: '5%',
          containLabel: true
        },
        xAxis : [
          {
            type : 'category',
            splitLine:{show: false},
            data: this.xData,
            axisLabel:{
              textStyle:{
                color:"#666",
                fontSize :12
              }
            },
            axisLine:{
              lineStyle:{
                color:"#999"
              }
            },
            axisTick: {
              alignWithLabel:true
            }
          }
        ],
        yAxis : [
          {
            axisLabel:{
              show:true,
              interval: 'auto', 
              formatter: '{value}', 
              textStyle:{
                color:"#666",
                fontSize : 14
              }
            },
            axisLine:{
              lineStyle:{
                color:"#999"
              }
            },
            splitLine:{show: false},
            type : 'value'
          }
        ],
        series : [
            {
                name:'历史客流量',
                label: {
                    normal: {
                      color:"rgba(255,255,255,0)",
                      show: true,
                      position: 'top'
                    }
                },
                type:'bar',
                barWidth : 30,
                data:this.sData,
                itemStyle: {
                  color: function(params) { 
                    var colorList = [ '#159CCC' ];
                    return colorList[params.dataIndex % 2]
                  }
                },
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                },
                animationDelayUpdate: function (idx) {
                  return idx * 50;
                }
            }
        ]
      });
    },
    getItme1(index){
      if( index == 0 ){
        this.showPrise = true;
        this.showRentPrise = false;
      }else{
        this.showPrise = false;
        this.showRentPrise = true;
      }
      this.activeClass1 = index;
    },
    getItme(index) {
      switch(index){
        case 0:
          this.aKey = '日';
          break;
        case 1:
          this.aKey = '周';
          break;
        case 2:
          this.aKey = '月';
          break;
        case 3:
          this.aKey = '季';
          break;
        default:
          this.aKey = '年';
          break;
      }
      this.activeClass = index;  // 把当前点击元素的index，赋值给activeClass
    },
    getChildCon:function(str){
      this.fKey=str;
    },
    fnData(){
      this.progress = this.$attrs.data.hotsub;
    },
    queryFilter (prop, key, arr) {
      if (!key) {
        return arr
      }
      arr = arr.filter((progress) => {
        if (progress[prop].toString().indexOf(key) !== -1) {
          return true
        }
      })
      if (key !== this[prop]) {
        this.currentPage = 0
        this[prop] = key
      }
      return arr
    }
  },
  created(){
    this.fnData();
  },
  mounted () {
    
  }
}
</script>

<style scoped>
#myChart1{
  width: 96%;
  height: 700px;
  margin: 0 auto;
}
ul.time{
  width: auto;
  height: 38px;
}
ul.time li{
  text-align: center;
  width: 80px;
  height: 36px;
  background: rgb(245, 247, 247);
  cursor: pointer;
  border: 1px solid rgb(196, 205, 216);
  line-height: 36px;
  font-size: 14px;
  float: left;
  margin:0 -1px 0 0;
}
ul.time li.active{
  color: #fff;
  background:#0fb1d6;
}
ul.time li:first-child{
  border-radius: 5px 0px 0px 5px;
}
ul.time li:last-child{
  border-radius: 0px 5px 5px 0px;
}
.alarm_set_tab{
  width: 100%;
  height: 36px;
  margin:0 0px 15px 0;
}
.alarm_set_tab a{
  width:96px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  display: block;
  background:url(../assets/img/alarm_set_tab.png) no-repeat;
  color: #792a04;
  font-size:15px;
  font-family:"微软雅黑";
  float:left;
  margin-left: 10px;
}
.alarm_set_tab a:hover{
  opacity:0.8;filter:alpha(opacity=80);-moz-opacity:0.8;-khtml-opacity:0.8;
}
.right_main_tab1_top p{
  float:right;
  margin: 9px 10px 0 0;
}
.right_main_tab1_top p a {
  float: left;
  width: 27px;
  height: 27px;
  margin-right: 10px;
  margin-top: 7px;
  background: url(../assets/img/line_btn.png) no-repeat;
  cursor: pointer;
}
.right_main_tab1_top p a:last-child{
  margin-right: 0px;
  background: url(../assets/img/table_btn.png) no-repeat;
}
.right_main_tab1_top p a.active {
  background-position: 0px -27px;
}
.right_main_tab1 table thead tr th,.right_main_tab1 table tbody tr td{
  text-align: center;
}
.right_main_tab1 table.da tbody tr td{
  font-size: 32px;
  color: #159ccc;
}
#index_bottom1_pie_bj{
  width: 700px;
  height:500px;
  margin:0 auto;
}
</style>



