<template>
  <div :style="{width: iW+'px'}" class="box_list"></div>
</template>

<script>
import echarts from 'echarts'
export default {
  props: {
    country: {},
    topTenPro: {},
    iW:String
  },
  data(){
    return{
      dataShadow:[],
      data:[],
      name:[],
      zong:0,              //总页数
      page:1,             //默认页数1
      num1:10,           //显示条数
      arrlist:[],        //把所有数据缓存下来并做处理         
    }
  },
  watch: {
    //国际
    country (val, oldVal) {
      this.zong = val.length;
      let timer = null;
      
      clearInterval(timer);
      timer = setInterval(()=>{
        this.data=[],this.name=[];
        this.arrlist = val;
        if( Number(this.num1 * this.page) == this.zong ){
          this.num1 = 10;
          this.page = 0;
        }
        this.arrlist = this.arrlist.slice(Number(this.num1 * this.page), Number(this.num1 * (this.page + 1)))
        for(let i=0;i<this.arrlist.length;i++){
          this.data.unshift(this.arrlist[i].percentage)
          this.name.unshift([(Number(this.num1 * this.page+i+1)), this.arrlist[i].country])
        }
        let max = Math.max.apply(null, this.data);
        let yMax = max+max*0.1;
        for (var i = 0; i < this.data.length; i++) {
          this.dataShadow.unshift(yMax);
        }
        this.drawLine(0)
        this.page++;
      }, 5000);

      for(let i=0;i<val.length;i++){
        if(i<10){
          this.data.unshift(val[i].percentage)
          this.name.unshift([(i+1), val[i].country])
        }
      } 
      let max = Math.max.apply(null, this.data);
      let yMax = max+max*0.1;
      for (var i = 0; i < this.data.length; i++) {
        this.dataShadow.unshift(yMax);
      }
      this.drawLine(0)
    },
    //国内
    topTenPro (val, oldVal){
      for(let i=0;i<val.length;i++){
        this.data.unshift(val[i].percentage)
        this.name.unshift([(i+1), val[i].city])
      } 
      let max = Math.max.apply(null, this.data);
      let yMax = max+max*0.1;
      for (var i = 0; i < this.data.length; i++) {
        this.dataShadow.unshift(yMax);
      }
      this.drawLine(1)
    }
  },
  methods: {
    drawLine (y) {
      let myChart = echarts.init(document.querySelectorAll('.box_list')[y])
      myChart.setOption({
        grid: {
            top: '0%',
            left:'0',
            width: '87%',
            containLabel: true
        },
        xAxis : [
            {
              type : 'value',
              axisLabel:{
                formatter:function(value) {
                  return Math.abs(value);
                }
              },
              splitLine:{show: false},
              axisLabel : { 
                formatter : '' 
              },
              show : false,
            },
            {
              type : 'value',
              axisLabel:{
                formatter:function(value) {
                  return Math.abs(value);
                }
              },
              splitLine:{show: false},
              axisLabel : { 
                formatter : '' 
              },
              show : false,
            }
        ],
        yAxis : [
          {
            type : 'category',
            axisTick : {
              onZero: false,
              lineStyle: {      
                  color: '#fff',
                  width: 0
              }
            },
            axisLabel:{ 
              textStyle:{
                  fontSize:12,
                  fontFamily : 'Arial,微软雅黑'
              }
            },
            data :this.name,
            splitLine:{show: false},
            axisLine:{  
                lineStyle:{  
                    color:'#3182b4',
                    width: 0
                }
            }
          }
        ],
        series : [
          {
            type:'bar',
            data:this.data,
            barWidth : 10,
            itemStyle: { 
              normal: {
                label: {
                    show: true,
                    position: 'right',
                    textStyle: {
                        fontSize : '12',
                        fontFamily : 'Arial,微软雅黑',
                        color: '#fff'
                    }
                },
                color: '#0b5d93'
              } 
            }
          }
        ]
      });
    }
  },
  mounted(){
  }
}
</script>

<style scoped>
.box_list {
  height: 122%;
  font-size: 14px;
  line-height: 30px;
  color: #47b2f9;
  padding:0 10px;
  position: absolute;
  left:0px;
  top:1%;
}
</style>



