<template>
  <div class="left_spot">
    <div class="header_like">
      <span>景点名称</span>
      <span>所属区域</span>
      <span>综合得分</span>
    </div>
    <div class="left_spot_co">
      <div class="pointList_scrollbar" style="display:none;">
        <span :style="{top: iTopSpan}"></span>
      </div>
      <div class="left_spot_co1">
        <ul :style="{top: iTop}">
          <li v-for="item in Array.prototype.reverse.call(abnormalSubscribers)" 
            :key="item.good"
          >
            <span>{{item.cn_name}}</span>
            <span>{{item.district_name}}</span>
            <em>{{item | format}}</em>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'scenicSpotRate',
  props:{
    right_bottom: {}
  },
  data () {
    return {
      abnormalSubscribers: [],
      iH:29,         //获取li的高度
      iTop: 0,       //设置ul的初始值
      index1: 0,     //设置li滚动个数从0开始
      iTopSpan: 0    //设置滚动条的初始值
    }
  },
  watch:{
    right_bottom(val, oldVal){
      this.abnormalSubscribers = val;
    }
  },
  methods: {
    fn() {    //自动滚动和移入移出暂停
      var timer = null;
      timer = setInterval(() => { scroll_news() }, 3000)
      let scroll_news=() => {
        this.index1++;
        this.iTop = -(this.iH*this.index1) + 'px'
        this.iTopSpan = Math.floor( this.index1*(260/(this.index-4)) ) + 'px';
        
        if ( this.index1+8 == this.index ) {
          this.iTop = 0 + 'px';
          this.iTopSpan = 0 + 'px';
          clearInterval(timer)
          this.index1 = 0;
          timer = setInterval(() => { scroll_news() }, 3000)
        }
      }
    }
  },
  filters: {
    format: function(value) {
      return ((value.good/value.total)*100).toFixed(2);
    }
  },
  mounted () {
    //this.fn()
  }
}
</script>

<style scoped>
.left_spot{
  width:96%;
  height:284px;
  margin:3px auto;
  overflow: hidden;
  border: 2px solid #003d5d;
  }
.header_like {
  height: 30px;
  font-size: 14px;
  padding: 0 2.5%;
  width: 95%;
  color: #b2e1ff;
  border-bottom: 1px solid #003d5d;
}
.header_like span{
  float: left;
  height: 30px;
  line-height: 30px;
  text-align: center;
  display: block;
  width: 20%;
}
.header_like span:nth-child(1){
  width: 60%;
}
@keyframes myfirst
{
  0%   {left:40px;opacity:1;}
  50%  {left:0px;opacity:0.4;}
  100% {left:-400px;opacity:0;}
}
@keyframes myfirst1
{
  0%   {left:-40px;opacity:0.2;}
  90%  {left:30px;opacity:0.9;}
  100% {left:40px;opacity:1;}
}
.left_spot_co ul li{
  width:100%;
  height:30px;
  font-size: 14px;
  border: 1px solid #003d5d;
}
.left_spot_co ul li:first-child p{
  border-top: 0 none;
}
.left_spot_co ul li p{
  width:100%;
  height:27px;
  border-bottom:1px solid #47b2f9;
}
.left_spot_co ul li span,.left_spot_co ul li em{
  display:block;
  float:left;
  height:27px;
  line-height: 27px;
  color: #47b2f9;
  font-family:Arial, "Microsoft YaHei", 黑体, 宋体, sans-serif;;
}
.left_spot_co ul li span{
  width: 64%;
  text-align: center;
}
.left_spot_co ul li span:nth-of-type(2){
  width: 20%;
  text-align: left;
}
.left_spot_co ul li em{
  width: 15%;
  font-style: normal;
}
.left_spot_co{
  width:100%;
  position: relative;
}
.left_spot_co1{
  width:100%;
  height: 254px;
  overflow: hidden;
  overflow-y: auto;
  position: relative;
  margin:0 auto;
}
.left_spot_co1::-webkit-scrollbar-thumb
{
    background-color:#47667a;
}
.left_spot_co1::-webkit-scrollbar-track {
  -webkit-box-shadow-color: hsla(0,0%,100%,.24);
  background-color: #164d71;
}
.left_spot_co1 ul {
  position: relative;
  left:0px;
  top: 0;
  -webkit-transition:.5s;
  transition:.5s;
}
.left_spot_btn{
  width:25px;
  height:45px;
  display:block;
  position:absolute;
  left: 458px;
  top: 536px;
}
.pointList_scrollbar {
  width: 4px;
  height: 100%;
  position: absolute;
  right: 0px;
  top: -1px;
  background-color: rgb(5, 94, 121,0.5);
  z-index: 999;
  overflow: hidden;
}
.pointList_scrollbar span{
  width: 4px;
  height: 100px;
  background-color:rgba(61,167,200,0.5);
  position: absolute;
  top: 0;
  right:0px;
  cursor: pointer;
  -webkit-transition: opacity .2s ease-in-out, background-color .2s ease-in-out;
  transition:opacity .2s ease-in-out, background-color .2s ease-in-out;
  z-index: 1000;
  border-radius: 5px;
}
.pointList_scrollbar:hover span{ background-color:rgba(61,167,200,1);}
</style>



