<template>
  <div>
    <span>{{area}}</span>
    <select
      v-model="selectModel" 
      @change="getContent($event)"
    >
      <option
        v-for="(item, index) of selectList"
        :key="index"
        :value="item.value"
      >
        {{item.text}}
      </option>
    </select>
  </div>
</template>

<script>
export default {
  props: {
    selectList: Array,
    area: String
  },
  data() {
    return {
      selectModel: ''
    }
  },
  methods:{
    getContent:function(e){
      this.$emit('tSelect',this.selectModel);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
select {
  width:145px;
  height:30px;
  border: 1px solid #e6e6e6;
  margin:0 10px 0 0;
  font-size:14px;
  font-family: "微软雅黑";
  color: #555;
  text-indent: 3px;
  float:left;
  background: 0 none;
}
span{
  display: block;
  color: #545454;
  font-size: 14px;
  font-family: "微软雅黑";
  height: 30px;
  line-height: 30px;
  float: left;
  margin: 0 5px 0 0;
}
</style>



