<template>
  <div id="box_map"></div>
</template>

<script>
import echarts from 'echarts'
import echartsGl from 'echarts-gl'
import geoCoord from '../views/js/geoCoord.js'
export default {
  props: {
    country: {}
  },
  data(){
    return {
      routes:[]
    }
  },
  watch:{
    country (val, oldVal) {
      for(let i=0;i<val.length;i++){
        if(geoCoord[val[i].country] != undefined && geoCoord[val[i].country] != null){
          let temp = new Array();
              temp.push([119.725056,30.234302]);
              temp.push(geoCoord[val[i].country]);
              this.routes.push(temp);
        }
      }
      this.drawLine();
    }
  },
  methods: {
    drawLine () {
      var option = {
        visualMap: {
            show: false,
            min: 0,
            max: 100,
            inRange: {
                symbolSize: [1.0, 10.0]
            }
        },
        globe : { //地球组件。组件提供了地球的绘制以及相应的坐标系
          globeRadius : 100, //地球的半径
          globeOuterRadius : 150, //地球的外半径
          baseTexture : '../static/img/map.jpg', //地球的纹理。支持图片路径字符串，图片或者 Canvas 的对象。
          heightTexture : '../static/img/map1.jpg', //地球的高度纹理。高度纹理可以用于凹凸贴图表现地球表面的明暗细节
          shading : 'lambert', //地球中三维图形的着色效果
          light : { //光照相关的设置。在 shading 为 'color' 的时候无效
            ambient : { //全局的环境光设置。
              intensity : 0.8 //环境光的强度
            },
            main : { //场景主光源的设置，在 globe 组件中就是太阳光。
              intensity : 0.8 //主光源的强度。
            }
          },
          viewControl : { //用于鼠标的旋转，缩放等视角控制。
            autoRotate : true, //是否开启视角绕物体的自动旋转查看。
            autoRotateSpeed : 20 //物体自传的速度。默认10 值越大速度越快
          }
        },
        series : {
          type : 'lines3D',
          coordinateSystem : 'globe', //使用的坐标系
          blendMode : 'lighter',
          lineStyle : { //线条样式。
            width : 1.5,
            color : 'rgb(139, 204, 245)',
            opacity : 0.7
          },
          data : this.routes
        },
        noDataLoadingOption:{
          text: '暂无数据',
              effect: 'dynamicLine'
          }
      };
      var rightMapChar  = echarts.init(document.getElementById('box_map'));
      rightMapChar.setOption(option);
    }
  },
  mounted(){
  }
}
</script>

<style scoped>
#box_map {
  width: 350px;
  height: 100%;
  margin:0 10px 0 0;
  float: right;
}
</style>



