<template>
  <div class="center">
    <div class="right_main">
      <div class="right_main_tab">
        <div class="alarm_set_tab">
          <!-- <to-input 
            @sInput="getChildCon"
            input-size="选择日期" 
            input-placeholder="请选择日期"
          ></to-input> -->
          <!-- <date-picker  @childFn="parentFn"></date-picker> -->
          <ul class="time">
            <li 
              :class="activeClass == index ? 'active':''" 
              v-for="(itme,index) in itmeList" 
              :key="index" 
              @click="getItme(index)"
            >{{itme}}</li>
          </ul>
        </div>
        <div class="right_main_tab1">
          <div class="right_main_tab1_top">
            <h2>交通方式分析</h2>
            <p>
              <a 
                :class="activeClass1 == index ? 'active':''" 
                v-for="(itme,index) in itmeList1" 
                :key="index" 
                @click="getItme1(index)"
                :title="itme"
              ></a>
            </p>
          </div>
          <div v-if="filteredUsers" class="tab_overflow" :style="'max-height:'+ Height">
            <div v-show="showPrise" id="myChart1" :style="{ height: !filteredUsers.length ? '0px' : '700px' }"></div>
            <table v-show="showRentPrise" style="display:none;">
              <thead>
                <tr>
                  <th>日期</th>
                  <th>自驾</th>
                  <th>长途汽车</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(user, index) in filteredUsers" :key="index">
                  <td>{{ user.lasttime }}</td>
                  <td>{{ user.transbus | sum }}</td>
                  <td>{{ user.transcar | sum }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="!filteredUsers.length" style="margin:10px auto;color:#f00;text-align:center;">暂无数据</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import echarts from 'echarts'
import ToInput from '../components/ToInput'
export default {
  name: 'vipSource',
  components: {
    ToInput
  },
  props: {
    listData:{},
    routerHeight: ''
  },
  data () {
    return {
      showPrise:true,
      showRentPrise:false,
      itmeList1:['统计图表','统计表格'],
      itmeList:['日','月','季','年'],
      activeClass: 0,
      activeClass1: 0,
      Height: parseInt(this.routerHeight) - 170 +'px',
      sData:[],
      sData1:[],
      xData:[],
      progress: [],
      fKey: '',             // 过滤 name 的关键字
      lasttime: '',          // 上一次过滤的 name 关键字，初始化为''
      aKey: '日',           // audit 的关键字
      name: '',             // 上一次过滤的 audit关键字，初始化为''
    }
  },
  watch: {
    progress: {
      handler () {
        this.progress
      },
      deep: true
    }
  },
  // 计算属性computed
  computed: {
    filteredUsers () {
      let fUsers = this.queryFilter('lasttime', this.fKey, this.progress)
          fUsers = this.queryFilter('name', this.aKey, fUsers)
      //let fUsers = this.queryFilter('name', this.aKey, this.progress)
      this.xData = [];
      this.sData = [];
      this.sData1 = [];
      for(let i=0;i<fUsers.length;i++){
        this.xData.push( fUsers[i].lasttime )
        this.sData.push( Number(fUsers[i].transbus) )
        this.sData1.push( Number(fUsers[i].transcar) )
      }
      setTimeout(()=>this.drawLine(), 100)
      return fUsers;
    }
  },
  methods: {
    parentFn(payload) {
      this.fKey = payload;
    },
    drawLine () {
      let myChart = echarts.init(document.getElementById('myChart1'))
      myChart.setOption({
        grid:{
          top:'-10%',
          left:'13%',
          right:'3%',
          bottom: '0%'
        },
        legend: {
          data:['邮件营销','联盟广告','视频广告','直接访问','搜索引擎']
        },
        tooltip: {
          trigger: 'axis'
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: true, 
          data: this.xData,
          axisTick:{
            lineStyle:{
              width:0 
            }
          },
          axisLine:{  
              lineStyle:{
                width:0,  
                color:'#000'  
              }
          }
        },
        yAxis: {
          type: 'value',
          axisTick:{
            lineStyle:{
              width:0 
            }
          },
          axisLine:{  
              lineStyle:{
                width:0,  
                color:'#000'  
              }
          }
        },
        series: [
          {
            name:'自驾',
            type:'line',
            color:'#2f7edb',
            symbol: 'circle',     //设定为实心点
            symbolSize: 8,   //设定实心点的大小
            data:this.sData1
          },
          {
            name:'长途汽车',
            type:'line',
            color:'#000',
            symbol: 'circle',     //设定为实心点
            symbolSize: 8,   //设定实心点的大小
            data:this.sData
          }
        ]
      });

      // var ecConfig = echarts.config;
      // myChart.on(ecConfig.EVENT.LEGEND_SELECTED, function(obj) {
      //   console.log(12212222)
      // });
    },
    getItme1(index){
      if( index == 0 ){
        this.showPrise = true;
        this.showRentPrise = false;
      }else{
        this.showPrise = false;
        this.showRentPrise = true;
      }
      this.activeClass1 = index;
    },
    getItme(index) {
      switch(index){
        case 0:
          this.aKey = '日';
          break;
        case 1:
          this.aKey = '月';
          break;
        case 2:
          this.aKey = '季';
          break;
        default:
          this.aKey = '年';
          break;
      }
      this.activeClass = index;  // 把当前点击元素的index，赋值给activeClass
    },
    getChildCon:function(str){
      this.fKey=str;
    },
    fnData(){
      this.progress = this.$attrs.data.traffic;
      //console.log(this.progress)
    },
    queryFilter (prop, key, arr) {
      // none query string, return arr
      if (!key) {
        return arr
      }
      // filtering
      arr = arr.filter((progress) => {
        if (progress[prop].toString().indexOf(key) !== -1) {
          return true
        }
      })
      // if it's a new filter query, research and turn to page one
      if (key !== this[prop]) {
        this.currentPage = 0
        // save last filter query
        this[prop] = key
      }
      return arr
    }
  },
  mounted () {
  },
  created(){
    this.fnData();
    this.parentFn();
  }
}
</script>

<style scoped>
#myChart1{
  width: 96%;
  height: 700px;
  margin: 0 auto;
}
ul.time{
  width: auto;
  height: 38px;
  float: left;
}
ul.time li{
  text-align: center;
  width: 80px;
  height: 36px;
  background: rgb(245, 247, 247);
  cursor: pointer;
  border: 1px solid rgb(196, 205, 216);
  line-height: 36px;
  font-size: 14px;
  float: left;
  margin:0 -1px 0 0;
}
ul.time li.active{
  color: #fff;
  background:#0fb1d6;
}
ul.time li:first-child{
  border-radius: 5px 0px 0px 5px;
}
ul.time li:last-child{
  border-radius: 0px 5px 5px 0px;
}
.alarm_set_tab{
  width: 100%;
  height: 36px;
  margin:0 0px 15px 0;
}
.alarm_set_tab a{
  width:96px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  display: block;
  background:url(../assets/img/alarm_set_tab.png) no-repeat;
  color: #792a04;
  font-size:15px;
  font-family:"微软雅黑";
  float:left;
  margin-left: 10px;
}
.alarm_set_tab a:hover{
  opacity:0.8;filter:alpha(opacity=80);-moz-opacity:0.8;-khtml-opacity:0.8;
}
.right_main_tab1_top p{
  float:right;
  margin: 9px 10px 0 0;
}
.right_main_tab1_top p a {
  float: left;
  width: 27px;
  height: 27px;
  margin-right: 10px;
  margin-top: 7px;
  background: url(../assets/img/line_btn.png) no-repeat;
  cursor: pointer;
}
.right_main_tab1_top p a:last-child{
  margin-right: 0px;
  background: url(../assets/img/table_btn.png) no-repeat;
}
.right_main_tab1_top p a.active {
  background-position: 0px -27px;
}
.right_main_tab1 table thead tr th,.right_main_tab1 table tbody tr td{
  text-align: center;
}
</style>



