import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import distribution from '@/components/distribution'

import about from '@/components/about'
import aboutIndex from '@/views/aboutIndex'
import study from '@/views/study'
import work from '@/views/work'
import channel from '@/views/channel'
import mmsSource from '@/views/mms_source'
import vipSource from '@/views/vipSource'
import mmsSubscribers from '@/views/mmsSubscribers'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  scrollBehavior (to, from, savePosition) {
    if (savePosition) {
      return savePosition
    } else {
      return {x: 0, y: 0}
    }
  },
  linkActiveClass: 'activeAAA',
  routes: [
    {
      path: '/',
      component: HelloWorld
    },
    {
      path: '/distribution',
      component: distribution,
      alias: 'distribution'
    },
    {
      path: '/about',
      component: about,
      children: [
        {
          path: '', // 默认的子路由
          name: 'about',
          component: aboutIndex
        },
        {
          path: 'vipSource',
          name: 'VipSource',
          component: vipSource
        },
        {
          path: 'work',
          name: 'Work',
          component: work
        },
        {
          path: 'study',
          name: 'Study',
          component: study
        },
        {
          path: 'channel',
          name: 'channel',
          component: channel
        },
        {
          path: 'mmsSource',
          name: 'mmsSource',
          component: mmsSource
        },
        {
          path: 'mmsSubscribers',
          name: 'mmsSubscribers',
          component: mmsSubscribers
        }
      ]
    },
    {
      path: '*',
      redirect: {name: 'About'}
    }
  ]
})



