<template>
  <div class="hz_map">
    <div id="mainChar" ref="mainChar" style="width:100%;height:100%;"></div>
  </div>
</template>

<script>
import { filters } from '@/js/filters';
import BaiduMap from '@/js/map.js'
export default {
    name: 'hz_map',
    props: {
      county: {}
    },
    data() {
        return {
            arr: [],
            lng: 119.987575,
            lag: 30.266666,
            name:'',
            total:1890,
            good:2098,
            mapsty: [{
                "featureType": "all",
                "elementType": "labels.text.fill",
                "stylers": {
                    "color": "#006680"
                }
            }, {
                "featureType": "all",
                "elementType": "labels.text.stroke",
                "stylers": {
                    "color": "#070e16"
                }
            }, {
                "featureType": "background",
                "elementType": "all",
                "stylers": {
                    "color": "#17415eff"
                }
            }, {
                "featureType": "road",
                "elementType": "all",
                "stylers": {
                    "color": "#32475bff",
                    "lightness": 1
                }
            }, {
                "featureType": "boundary",
                "elementType": "all",
                "stylers": {
                    "color": "#32475bff"
                }
            }, {
                "featureType": "road",
                "elementType": "labels",
                "stylers": {
                    "color": "#006680ff"
                }
            }, {
                "featureType": "poilabel",
                "elementType": "labels",
                "stylers": {
                    "color": "#006680ff"
                }
            }, {
                "featureType": "land",
                "elementType": "geometry",
                "stylers": {
                    "color": "#112133ff",
                    "saturation": 1
                }
            }, {
                "featureType": "highway",
                "elementType": "all",
                "stylers": {
                    "color": "#32475bff"
                }
            }, {
                "featureType": "poi",
                "elementType": "all",
                "stylers": {
                    "visibility": "off"
                }
            }, {
                "featureType": "road",
                "elementType": "all",
                "stylers": {
                    "visibility": "off"
                }
            }]
        }
    },
    watch: {
        county(val, oldval) {
            val.forEach((item, index) => {
                this.arr.push(item.point + ', ' + item.cn_name + ', ' + item.good + ', ' + item.total)
            });
            let i = 0;
            let num = this.arr.length;
            
            let self=this;
            if (self && !self._isDestroyed) {
                setInterval(() => {
                    i++;
                    if (i == num) {
                        i = 0;
                    }
                    this.point = this.arr[i].split(', ')[0]
                    this.name = this.arr[i].split(', ')[1]
                    this.good = this.arr[i].split(', ')[2]
                    this.total = this.arr[i].split(', ')[3]
                    this.lng = Number(this.point.split(',')[0])
                    this.lag = Number(this.point.split(',')[1])
                    this.fnMap(this.name);
                    this.$refs.mainChar.style.background = 'initial'
                }, 5000)
            }
        }
    },
    methods: {
        fnMap(name){
            var map = new BMap.Map("mainChar", {
                enableMapClick: false,
                minZoom: 8,
                maxZoom: 16
            });
            map.centerAndZoom(new BMap.Point(this.lng, this.lag), 11); // 初始化地图,设置中心点坐标和地图级别
            // setTimeout(()=>{
            //     map.panTo(new BMap.Point(this.lng, this.lag));
            // }, 2000)
            map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
            map.setMapStyle({
                styleJson: this.mapsty
            });
            
            function label(div){
                div.setStyle({
                    color :"#fff",
                    fontSize :"16px",
                    height :"auto",
                    lineHeight :"24px",
                    fontFamily:"Arial, 微软雅黑",
                    border:'0 none',
                    padding: '8px 10px',
                    borderRadius:'4px',
                    backgroundColor : 'rgba(15,91,149,0.5)'
                });
            }
            var optsShcl = {
                position : new BMap.Point(this.lng, this.lag),
                offset   : new BMap.Size(0, 0)    
            }
            var labelShcl = new BMap.Label(`<div>${name}<p>昨日客流：${filters(this.good)} 人</p><p>今日客流：${filters(this.total)} 人</p></div>`, optsShcl);  
            map.addOverlay(labelShcl);
            label(labelShcl)

            getBoundary(name);
            function getBoundary(name, color) {
                var bdary = new BMap.Boundary();
                bdary.get(name, function(rs) {
                    var bounds;
                    var maxNum = -1,
                        maxPly;
                    var count = rs.boundaries.length;
                    for (var i = 0; i < count; i++) {
                        var ply = new BMap.Polygon(rs.boundaries[i], {
                            strokeWeight: 2,
                            strokeColor: "#aed2ff",
                            FillOpacity: 0
                        });
                        ply.setFillColor(color);
                        ply.setFillOpacity("0");
                        map.addOverlay(ply);
                    }
                    if (maxPly) {
                        map.setViewport(maxPly.getPoints());
                    }
                });
            }
        }
    },
    mounted() {
        BaiduMap.init().then((BMap) => {
            this.fnMap("西湖区");
            this.$refs.mainChar.style.background = 'initial'
        })
    }
}
</script>

<style>
.hz_map{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}
.anchorBL {
    display:none;
}
</style>




