<template>
  <div style="height: 100%;">
    <swiper :options="swiperOption" style="height: 100%;">
        <swiper-slide>
          <ahobby />
        </swiper-slide>

        <swiper-slide  v-for="item of swiperList" :key="item.id">
           <div class="body_center_co" v-for="item1 of item.lib" :key="item1.id">
              <div class="body_center_co1_zong">
                  <title-list size="道路一"></title-list>
                  <div class="body_center_co1"></div>
              </div>

             <div class="body_center_co1_zong">
                  <title-list size="道路二"></title-list>
                  <div class="body_center_co1"></div>
              </div>

              <div class="body_center_co1_zong">
                  <title-list size="道路三"></title-list>
                  <div class="body_center_co1"></div>
              </div>

              <div class="body_center_co1_zong">
                  <title-list size="道路四"></title-list>
                  <div class="body_center_co1"></div>
              </div>

             <div class="body_center_co1_zong">
                  <title-list size="道路五"></title-list>
                  <div class="body_center_co1"></div>
              </div>

              <div class="body_center_co1_zong">
                  <title-list size="道路六"></title-list>
                  <div class="body_center_co1"></div>
              </div>
           </div>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination" style="bottom:0px;"></div>
    </swiper>
  </div>
</template>

<script>
import axios from 'axios'
import echarts from 'echarts'
import Ahobby from '../views/ahobby'
import titleList from '../module/titleList'
import { filters,curDateTime } from '@/js/filters'
export default {
  components:{
    Ahobby,
    titleList,
  },
  data (){
    return{
      qnData:[],jnData:[],
      qnData1:[],jnData1:[],
      swiperOption: {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          paginationBulletRender: function (swiper, index, className) {
              return '<span style="width:15px;height:15px;margin: 0 10px;" class=' + className + '></span>';
          }
      },
      swiperList: [{
          id: '01',
          lib: [{
            name:'1'
          }]
      }]
    }
  },
  methods:{
    getVipSource () {
      axios.get('/static/mock/distribution1.json')
        .then(this.getVipSourceSucc)
    },
    getVipSourceSucc (res) {
      res = res.data
      if(res){
        this.jnList = res.jnList;
        let Time = parseInt(curDateTime().split(' ')[1].split(':')[0]);
        for(let i=0;i<this.jnList.length;i++){
          if( Time >= Number(this.jnList[i].timeHour) ){
            this.jnData.push([Number(this.jnList[i].timeHour), this.jnList[i].num*Math.random()]);
          }
        }
        
        this.qnList = res.qnList;
        this.qnList.forEach((item, index)=>{
          if( Time >= Number(item.timeHour) ){
            this.qnData.push([Number(item.timeHour), item.num*Math.random()]);
          }
        })

        this.jnList1 = res.jnList1;
        for(let i=0;i<this.jnList.length;i++){
          if( Time >= Number(this.jnList1[i].timeHour) ){
            this.jnData1.push([Number(this.jnList1[i].timeHour), this.jnList1[i].num*Math.random()]);
          }
        }
        
        this.qnList1 = res.qnList1;
        this.qnList1.forEach((item, index)=>{
          if( Time >= Number(item.timeHour) ){
            this.qnData1.push([Number(item.timeHour), item.num*Math.random()]);
          }
        })
      }
    },
    lineChart(y, _qnData, _jnData){
      let myChart = echarts.init(document.querySelectorAll('.body_center_co1')[y])
      myChart.setOption({
        tooltip : {
          trigger: 'axis',
          formatter: function(params, ticket, callback) {
            if(params.length == 1){
              return params[0].data[0] + ":00 <br/>" + params[0].seriesName + " : "  + filters(params[0].data[1]);
            }else if(params.length == 2){
              return params[0].data[0] + ":00 <br/>" + params[0].seriesName + " : " + filters(params[0].data[1]) + "<br/>" + params[1].seriesName + " : "  + filters(params[1].data[1]);
            }else{
              return "- : -";
            }
          }
        },
        legend: {
          right: 30,
          top:-5,
          itemWidth: 15,
          textStyle: {
            color:'#fff',
          },
          icon:'roundRect',
          selectedMode:false,
            data: ['去年', '今年']
        },
        grid: {
          top:'5%',
            left: '2%',  
            right: '4%',  
            bottom: '2%',  
            containLabel: true  
        },
        xAxis: {
          type : 'value',
          min : 0,
          max : 23,
          interval : 1, //步进
          axisLine:{  
            textStyle: {
                  color: '#fff'
              },
              lineStyle:{  
                  color:'#39576f'  
              }
          },   
          axisLabel:{  
              rotate:0,  
              interval:0,
              textStyle: {
                  color: '#ffffff'
              }
          }, 
          splitLine:{show: false},
        },  
        yAxis: { 
            splitLine:{
              show: true,
              lineStyle: {     
                color: ['#1b2e3c'],
                width: 1,
                type: 'solid'
              }
            },
            type: 'value' ,
            axisLabel : {
                textStyle: {
                    color: '#ffffff'
                }
            },
            axisLine:{
                lineStyle:{  
                  color:'#39576f'  
                }
            }
        },  
        series: [  
            {  
                name:'去年',  
                type:'line',
                symbol:'circle',
                symbolSize:13,   
                color:['#ff4359'], 
                data:_qnData,  
                smooth:false,   
                itemStyle:{  
                  normal:{  
                    lineStyle:{
                      width:1,  
                      type:'dotted' 
                    },
                  }  
                },
                animationDelay: function (idx) {
                  return idx * 100;
                }  
            },
            {  
                name:'今年',  
                type:'line',
                symbol:'circle',
                symbolSize:13,   
                color:['#fff000'], 
                data:_jnData,  
                smooth:false,   
                itemStyle:{  
                  normal:{  
                    lineStyle:{
                      width:1,  
                      type:'dotted'
                    },
                  }  
                },
                animationDelay: function (idx) {
                  return idx * 100;
                }  
            }

        ],
        animationEasing: 'elasticOut',
        animationDelayUpdate: function (idx) {
          return idx * 5;
        }  
      });
    }
  },
  mounted(){
    this.getVipSource();
    setTimeout(()=>{
      this.lineChart(0, this.qnData, this.jnData);
      this.lineChart(1, this.qnData1, this.jnData1);
      this.lineChart(2, this.qnData, this.jnData);
      this.lineChart(3, this.qnData1, this.jnData1);
      this.lineChart(4, this.qnData, this.jnData);
      this.lineChart(5, this.qnData1, this.jnData1);
    }, 100)
  }
}
</script>

<style scoped>
#centerArea{
  overflow: hidden;
}
.swiper-container{
  width: 100%;
  height: 1050px;
  background:url(../assets/img/bg.jpg) no-repeat;
}
.body_center_co {
  width: 1911px;
  height: 100%;
  margin: 10px 8px 0;
  color: #fff;
}
.body_center_co1_zong{
  width: 945px;
  height: 340px;
  float: left;
  margin: 0 0 10px;
  background: url(../assets/img/top_bg.png) no-repeat;
  background-size: 100% 100%;
}
.body_center_co1 {
  width: 930px;
  height: 300px;
  margin: 0 auto;
}
.body_center_co1_zong:nth-child(2n) {
  margin-left: 10px;
}
</style>



