<template>
  <div>
    <div class="right_main">
      <div class="right_main_tab">
        <div class="alarm_set_tab">
          <to-input 
            @sInput="getChildCon"
            input-size="搜索条件" 
            input-placeholder="请输入条件搜索"
          ></to-input>
        </div>
        <div class="right_main_tab1">
          <div class="right_main_tab1_top">
            <h2>连接管理</h2>
          </div>
          <div class="div1_top" style="width:100%;" :style="'height:'+ this.fullHeight">
            <i-echart :option="option" class="echarts-container"></i-echart>
            <views-header
             :rows="study" 
             :columns="columnData" 
             :filter-key="searchQuery"
             style="margin: 50px auto 0;"
            ></views-header>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ViewsHeader from './components/Header'
import iEchart from './components/Ehcart'
import ToInput from '../components/ToInput'
export default {
  name: '',
  components: {
    ViewsHeader,
    iEchart,
    ToInput
  },
  data () {
    return {
      fullHeight: document.documentElement.clientHeight - 210 + 'px',
      columnData: ['接入号','运营商','企业代码','网关信息','业务系统数量','连接数','状态','加入时间'],
      study: [],
      searchQuery: '',
      option: {},
      inputSize: String,
      inputPlaceholder: String,
      sWhether: String,
      ehcart:[]
    }
  },
  created(){
    setTimeout(()=>{
      let study1 = this.study
      let seriesArr = [],
          xAxisArr = []
      for(var i=0;i<study1.length;i++){
        seriesArr.push(study1[i].业务系统数量)
        xAxisArr.push(study1[i].运营商)
      }

      //console.log(this.study)
      xAxisArr.sort(function (a, b){
        return a-b;
      })
      var formatterMax;
      this.option={
        grid: {
          left: '2%',
          right: '0%',
          bottom: '0%',
          top:'20px',
          containLabel: true
        },
        tooltip : {
          backgroundColor : '#19486e',
          borderColor : '#228f96',
          borderRadius : 10,
          borderWidth: 1,
          textStyle:{
            fontSize:16,
            align:'center'
          },
          trigger: 'item',
          formatter: '{b} <br/>{a} : {c}'
        },
        xAxis: {
          axisLabel:{
            textStyle:{
              color:"#666",
              fontSize :14
            }
          },
          data: xAxisArr
        },
        yAxis: {
          axisLabel:{
            formatter: function (value, index) {
              formatterMax=value;
                return value;
            },
            textStyle:{
              color:"#666",
              fontSize : 14
            }
          },
          splitLine:{show: false},
          type : 'value'
        },
        series: [{
          name: '业务系统数量',
          type: 'bar',
          barWidth: '15%',
          data: seriesArr,
          itemStyle: {
            color: 'rgba(23,171,224,1)'
          },
          animationDelay: function (idx) {
            return idx * 100;
          }
        }]
      }
    },500)
  },
  methods: {
    //子组件:通过$emit绑自定事件实现子级向父级传递数据
    getChildCon:function(str){
      this.searchQuery=str;
      this.ehcart = str;
      this.getArr(this.ehcart);
    },
    fnData(){
      this.study = this.$attrs.data.study;
    },
    getArr(data){
      if (data) {
      }
    }
  },
  mounted () {
    this.fnData();
  }
}
</script>

<style scope>
.div1_top{
  overflow-y:auto
}
.alarm_set_tab{
  width: 100%;
  height: 30px;
  margin:0 0px 15px 0;
}
.echarts-container{
  width: 100%;
  height: 400px;
}
</style>



