<template>
  <div>
    <span>{{inputSize}}</span>
    <input 
      v-model="inputModel" 
      @input="getContent($event)"
      type="text" 
      :placeholder="inputPlaceholder"
    />
  </div>
</template>

<script>
export default {
  props: {
    inputSize: String,
    inputPlaceholder: String
  },
  data() {
    return {
      inputModel: ''
    }
  },
  methods:{
    getContent:function(e){
      this.$emit('sInput',this.inputModel);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
input{
  width: 190px;
  height: 28px;
  border: 1px solid #e6e6e6;
  float: left;
  padding: 0 4px;
  color: #555;
  font-size: 14px;
  font-family: Arial,Verdana,"微软雅黑";
  margin: 0 10px 0 0;
}
span{
  display: block;
  color: #545454;
  font-size: 14px;
  font-family: "微软雅黑";
  height: 30px;
  line-height: 30px;
  float: left;
  margin: 0 5px 0 0;
}
</style>



