<template>
  <div id="container">
    <div class="body_center_cos">
      <title-list size="去年同期道路通行人数对比"></title-list>
      <p>( {{sHour}}  - {{eHour}} )</p>
      <ul class="six_month">
        <li v-for="(item, index) in jnData" :key="index">
          <img v-if=" Math.floor((item[1]/formatterMax).toFixed(2)*244) < 70 " :style="{height: Math.floor((item[1]/formatterMax).toFixed(2)*244)+'px'}" src="../assets/img/traffic_transportation_icon3.png" />
          <img v-else :style="{height: Math.floor((item[1]/formatterMax).toFixed(2)*244)+'px'}" src="../assets/img/traffic_transportation_icon1.png" />
        </li>
      </ul>
      <div id="body_center_echarts1"></div>
    </div>

    <div class="body_center_cos">
      <title-list size="近7天景区境内游客城市排名"></title-list>
      <div id="body_center_echarts2">
        <center-move :topTenPro="historyLydList" iW="650" LR="left"></center-move>
        <ul>
          <li v-for="(item, index) in historyLydList" :key="index">
            {{ item.dataName }}:{{ item.percent }}%
          </li>
        </ul>
      </div>
    </div>

    <div class="body_center_cos">
      <title-list size="近7天景区游客年龄分布"></title-list>
      <ul class="body_center_echarts">
        <li v-for="(item, index) in hisAge" :key="index">
          {{ item.ageName }}({{ (item.num*100 / ageTotal).toFixed(2) }}%)
        </li>
      </ul>
    </div>

    <div class="body_center_cos">
      <title-list size="近7天景区游客来源地分布"></title-list>
      <div id="body_center_echarts4"></div>
    </div>
    
    <div class="body_center_cos">
      <title-list size="近7天景区游客性别分布"></title-list>
      <div id="body_center_echarts5">
        <div id="body_center_echarts5_pic"></div>
      </div>
    </div>

    <div class="body_center_cos">
      <title-list size="近7天景区游客停留时长分布"></title-list>
      <div id="body_center_echarts6"></div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import echarts from 'echarts'
import titleList from '../module/titleList'
import CenterMove from '../components/Centermove'
import { filters,curDateTime,getBeforeDate } from '@/js/filters';
export default {
  name:'',
  components: {
    titleList,
    CenterMove
  },
  data(){
    return {
      seriesArr:[],xAxisArr:[],formatterMax:0,xData: [],jnData:[],sHour:'',eHour:'',
      historyLydList:[], markLineDate:[],
      hisAge: [],ageTotal:0,
      swYks:0,jwYks:0,snYks:0,
      female:0, male:0,
      dataShadow:[],data:[]
    }
  },
  methods:{
    getVipSource () {
      axios.get('/static/mock/distribution.json')
        .then(this.getVipSourceSucc)
    },
    getVipSourceSucc (res) {
      res = res.data
      if(res){
        this.jnList = res.jnList
        this.qnName = res.qnName
        this.jnName = getBeforeDate(0)
        for(var i=0;i<this.jnList.length;i++){
          this.jnData.push([this.jnList[i].areaName, this.jnList[i].num]);
          this.xData.push(this.jnList[i].areaName);
        }
        this.sHour = (curDateTime().split(' ')[1].split(':')[0] - 1) +":00";
        this.eHour = curDateTime().split(' ')[1].split(':')[0] +":00";

        this.hisAge = res.hisAge  
        for(var i =0;i<res.hisAge.length;i++){
          this.ageTotal += Number( res.hisAge[i].num )
        }

        this.hisZlsj = res.hisZlsj
        this.data.push(this.hisZlsj.sjA,this.hisZlsj.sjB,this.hisZlsj.sjC,this.hisZlsj.sjD,this.hisZlsj.sjE);
        let max = Math.max.apply(null, this.data);
	      let yMax = max+max*0.15;
        for (var i = 0; i < this.data.length; i++) {
          this.dataShadow.push(yMax)
        }

        this.hisGender = res.hisGender;
        for(let i=0;i<this.hisGender.length;i++){
          if( this.hisGender[i].genderId == 1 ){
            this.male = this.hisGender[i].num;
          }else if( this.hisGender[i].genderId ==2 ){
            this.female = this.hisGender[i].num;
          }
        }

        this.hisLyd = res.hisLyd;
        for(let i=0;i<this.hisLyd.length;i++){
          if( this.hisLyd[i].dataType == 0 ){
            this.swYks = this.hisLyd[i].dataNum;
          }else if( this.hisLyd[i].dataType == 2 ){
            this.jwYks = this.hisLyd[i].dataNum;
          }
        }
        this.hisSnLyd = res.hisSnLyd;
        this.hisSnLyd.forEach((item, index)=>{
          if( item.dataType == 1 ){
            this.snYks = item.dataNum;
          }
        })

        this.historyLydList = res.historyLydList;
      }
    },
    myChart1(){
      var formatterMax;
      var myChart1 = echarts.init(document.getElementById('body_center_echarts1')); 
      var option1 = {
        tooltip : {
          trigger: 'axis',
          formatter: function(params, ticket, callback) {
            if(params.length == 1){
              return params[0].seriesName + " <br/> " +  params[0].data[0]　+ " : " + filters(params[0].data[1]);
            }else{
              return "- : -";
            }
          }
        },
        legend: {
          right: 5,
          itemWidth: 15,
          textStyle: {
            color:'#fff',
          },
          selectedMode:false,
          data: [this.jnName]
        },
        grid: {
            top:'10%',
            left: '3%',
            right: '4%',
            bottom: '2%',
            containLabel:true
        },
        color:['#eccd3c'],
        xAxis : [
            {
                type : 'category',
                splitLine:{show: false},
                axisLine: {
                    lineStyle: {
                        type: 'solid',
                        color:'#39566f',
                        width:'1'
                    }
                },
                axisLabel: {
                    rotate: 0,
                    textStyle: {
                        color: '#ffffff'
                    }
                },
                data : this.xData
            }
        ],
        yAxis : [
            {
              splitLine:{
                  show: true,
                  lineStyle: {   
                      color: ['#1b2e3c'],
                      width: 1,
                      type: 'solid'
                  }
                },
                type : 'value',
                splitLine:{show: false},
                axisLine: {
                    lineStyle: {
                        type: 'solid',
                        color:'#39566f',
                        width:'1'
                    }
                },
                axisLabel: {
                    formatter: function (value, index) {
                      formatterMax = value;
                      return value;
                    },
                    textStyle: {
                        color: '#ffffff'
                    }
                }
            }
        ],
        series : [
            {
                name:this.jnName,
                type:'bar',
                data:this.jnData,
                barWidth : 46,
                itemStyle: {
                  color: 'rgba(23,171,224,0)'
                },
                label: {
                    normal: {
                      show: true,
                      position: 'top',
                      formatter: function(params, callback) {
                        return params.data[1];
                      }
                    }
                },
                barGap: '0%'
            }
        ],
        noDataLoadingOption : {
          text: '暂无数据',
          effect : 'dynamicLine'
        }
      };  
      myChart1.setOption(option1,true);

      this.formatterMax = formatterMax;
      for(let i=0;i<this.jnData.length;i++){
        this.seriesArr.push( filters(this.jnData[i][1]) )
        this.xAxisArr.push( this.jnData[i][0] )
      }
    },
    myChart4(){
      var myChart1 = echarts.init(document.getElementById('body_center_echarts4'));
      var option1 = {
	        color:['#49dff0', '#ed4d4d','#edce3c'],
          series : [
              {
                  name: '',
                  type: 'pie',
                  radius : '90%',
                  center: ['50%', '50%'],
                  data:[
                      {value:this.swYks, name:'省外'},
                      {value:this.jwYks, name:'境外'},
                      {value:this.snYks, name:'省内'}
                  ],
                  animation: false,
                  label: {
                      normal: {
                          formatter: '{b|{b}：}({per|{d}%})',
                          rich: {
                              a: {
                                  color: '#999',
                                  lineHeight: 0,
                                  align: 'center'
                              },
                              b: {
                                  fontSize: 14,
                                  lineHeight: 0
                              },
                              per: {
                                  fontSize: 14,
                                  padding: [2, 4],
                                  borderRadius: 2
                              }
                          }
                      }
                  }
              }
          ]
      };
      myChart1.setOption(option1,true);
    },
    myChart5(){
      var myChart3 = echarts.init(document.getElementById('body_center_echarts5_pic'));
      var option3 = {
        series: [
              {
                  name:'',
                  type:'pie',
                  radius: ['60%', '85%'],
                  color:['#e25353','#49dff0'],
                  label: {
                      normal: {
                          formatter: '{b|{b}：}({per|{d}%})',
                          rich: {
                              a: {
                                  color: '#999',
                                  lineHeight: 0,
                                  align: 'center'
                              },
                              b: {
                                  fontSize: 14,
                                  lineHeight: 0
                              },
                              per: {
                                  fontSize: 14,
                                  padding: [2, 4],
                                  borderRadius: 2
                              }
                          }
                      }
                  },
                  animation: false,
                  data:[
                      {value:this.female, name:'女性'},
                      {value:this.male, name:'男性'}
                  ],
              noDataLoadingOption:{
                text: '暂无数据',
                    effect: 'dynamicLine'
              }
              }
          ]
        };
      myChart3.setOption(option3);
    },
    myChart6(){
      var myChart5 = echarts.init(document.getElementById('body_center_echarts6'));
      myChart5.setOption({
        grid: {
        top: '0%',
          left: '12%',
          right: '-5%',
          bottom: '0%',
          containLabel: false
        },
        xAxis : [
            {
              type : 'value',
              axisLabel:{
                formatter:function(value) {
                  return Math.abs(value);
                }
              },
              splitLine:{show: false},
              axisLabel : { 
                formatter : '' 
              },
              show : false,
            },
            {
              type : 'value',
              axisLabel:{
                formatter:function(value) {
                  return Math.abs(value);
                }
              },
              splitLine:{show: false},
              axisLabel : { 
                formatter : '' 
              },
              show : false,
            }
        ],
        yAxis : [
          {
            type : 'category',
            axisTick : {
              onZero: false,
              lineStyle: {      
                  color: '#fff',
                  width: 0
              }
            },
            axisLabel:{ 
              textStyle:{
                  fontSize:14
              }
            },
            data : ['2小时','3小时','4小时','5小时','5小时以上'],
            splitLine:{show: false},
            axisLine:{  
                lineStyle:{  
                  color:'#fff',
                  width: 0,
                  type: 'solid'
                }
            }
          }
        ],
        series : [
          {
              type: 'bar',
              itemStyle: {
                  normal: {
                    color: 'rgba(0,0,0,0.5)',
                    barBorderRadius:[20, 20, 20, 20]
                  }
              },
              barGap:'-100%',
              barCategoryGap:'0%',
              data:this.dataShadow,
              barWidth : 33,
              animation: false
          },
          {
            type:'bar',
            data:this.data,
            barWidth : 30,
            itemStyle: { 
              normal: {
                barBorderRadius:[20, 20, 20, 20],
                label: {
                    show: true,
                    formatter: function(p) {
                      return filters(p.value)+"人次";
                    },
                    position: 'right',
                    textStyle: {
                      fontSize : '14',
                      fontFamily : 'Arial,微软雅黑',
                      color: '#fff'
                    }
                },
                color: function(params) { 
                  var colorList = [ '#d18120','#bcaf0b','#17a499','#01ba7b','#129aed']; 
                  return colorList[params.dataIndex] 
                }
              } 
            }
          }
        ]
      });
    }
  },
  mounted(){
    this.getVipSource();
    setTimeout(()=>{
      this.myChart1();
      this.myChart4();
      this.myChart5();
      this.myChart6();
    }, 500)
  }
}
</script>

<style scoped>
#container{
  width: 1911px;
  height: 100%;
  margin: 10px 8px 0;
  color: #fff;
}
.body_center_cos {
    width: 945px;
    height: 340px;
    float: left;
    margin: 0 0 10px;
    background: url(../assets/img/top_bg.png) no-repeat;
    background-size: 100% 100%;
}
.body_center_cos:nth-child(2n) {
    margin-left: 10px;
}
#body_center_echarts1,ul.body_center_echarts,
#body_center_echarts6,#body_center_echarts5,
#body_center_echarts4,#body_center_echarts2{
  width: 930px;
  height: 300px;
  margin: 0 auto;
}
#body_center_echarts2_l{
  height: 100%;
  float: left;
  margin: 0 30px 0 55px;
}
#body_center_echarts2 ul {
  width: 120px;
  height: 260px;
  float: right;
  background: rgba(32,105, 158, .7);
  margin: 12px 100px 0 0;
}
#body_center_echarts2 ul li {
  padding-right: 20px;
  font-size: 14px;
  line-height: 26px;
  text-align: right;
  color: #fff;
  font-family: Arial,黑体;
}
#body_center_echarts5_pic{
  width: 100%;
  height: 300px;
  margin: 0 auto;
  background: url(../assets/img/body_center_nn.png) no-repeat center center;
}
ul.body_center_echarts{
  position: relative;
  background: url(../assets/img/body_center_lft.png) no-repeat center center;
}
ul.body_center_echarts li{
	font-size:14px;
	font-family: Arial,黑体;
	text-align: center;
	position: absolute;
}
ul.body_center_echarts li:nth-child(1){
  right: 28.5%;
  top: 62px;
  color:#49ddbd;
}
ul.body_center_echarts li:nth-child(2){
  left: 26.5%;
  top: 113px;
  color:#4dbfe8;
}
ul.body_center_echarts li:nth-child(3){
	right: 19.5%;
    top: 148px;
    color: #858dd4;
}
ul.body_center_echarts li:nth-child(4){
  left: 19%;
  bottom: 98px;
  color:#5a67ce;
}
.body_center_cos:first-child{
  position: relative;
}
.body_center_cos:first-child p{
  font-family: Arial;
  font-size: 14px;
  color: #fff;
  position: absolute;
  left: 36px;
  top: 32px;
}
ul.six_month {
  width: 824px;
  height: 244px;
  position: absolute;
  left: 75px;
  top: 66px;
}
.six_month li{
  width:54px;
  height:100%;
  background-size: 100% 100%;
  float: left;
  position: relative;
  cursor:pointer;
}
.six_month li:first-child{
  margin: 0 0 0 40px;
}
.six_month li:not(:first-child){
  margin: 0 0 0 83px;
}
.six_month li:hover{
  opacity: 0.8;
}
.six_month li img{
  width: 100%;
  bottom:0;
  position: absolute;
  height:0;
}
</style>



