<template>
  <div>
    <div class="l_co">
      <p>{{name}}</p>
      <p>
        <span>昨日:</span>{{ yesterday | sum }}人 
        <br />
        +{{yesterdayratio}}%
      </p>
      <p>
        <span>上月:</span>{{ lastmonth | sum }}人 
        <br />
        +{{monthratio}}%
      </p>
      <p>
        <span>2017年:</span>
        <br />
        {{ yearday | sum }}人 
        <br />
        +{{ yearmon }}%
      </p>
      <p>
        <span>当前累计:</span>{{daynumber | sum }}人
      </p>
    </div>
    <div class="main_companel"></div>
  </div>
</template>

<script>
import echarts from 'echarts'
import { getBeforeDate } from '@/js/filters'
export default {
  data() {
    return {
      name:'',
      yearday:0,
      yearmon:0,
      yesterday:0,
      yesterdayratio:'',
      lastmonth:'',
      monthratio:'',
      daynumber:0,
      legendArr: [],
      color: ['#325B69', '#698570', '#AE5548', '#6D9EA8', '#9CC2B0', '#C98769'],
      sData:[], 
      xData:[],
      time:null
    }
  },
  methods: {
    fnData(){
      let i=0;
      let val1 = [this.$attrs.linexhyd, this.$attrs.lineqdhyd];
      clearInterval(this.time);
      this.time = setInterval(()=>{
        i++;
        if(i>1){
          i=0;
        }

        let val = val1[i];
        this.name = val.name;
        let month1;
        let valArr = val1[i]['month'+((i+2)-1)];
        for(let i=0;i<valArr.length;i++){
          this.lastmonth = valArr[1].value;
          month1 = valArr[0].value;
        }
        this.monthratio =  ((this.lastmonth / month1)*100).toFixed(2);

        let random='';
        let dayArr = val1[i]['day'+((i+2)-1)];
        this.sData=[],this.xData=[];
        for(let i=0;i<dayArr.length;i++){
          random = dayArr[ Math.floor(Math.random() * i) ];
          this.sData.push( Number( dayArr[i].value ) )
          this.xData.push( getBeforeDate(i) )
          this.daynumber += Number( dayArr[i].value )
        }
        let yearArr = val1[i]['year'+((i+2)-1)];
        for(let i =0;i<yearArr.length;i++){
          this.yearday = yearArr[0].value;
        }
        this.yesterday = random.value
        this.yesterdayratio = ((this.yesterday / this.daynumber)*100).toFixed(1)
        this.drawLine();
      }, 5000)

      let val = this.$attrs.linexhyd;
      this.name = val.name;
      let month1;
      for(let i=0;i<val.month1.length;i++){
        this.lastmonth = val.month1[1].value;
        month1 = val.month1[0].value;
      }
      this.monthratio =  ((this.lastmonth / month1)*100).toFixed(2);

      let random='';
      for(let i=0;i<val.day1.length;i++){
        random = val.day1[ Math.floor(Math.random() * i) ];
        this.sData.push( Number( val.day1[i].value ) )
        this.xData.push( getBeforeDate(i) )
        this.daynumber += Number( val.day1[i].value )
      }
      for(let i =0;i<val.year1.length;i++){
        this.yearday = val.year1[0].value;
      }
      this.yesterday = random.value;
      this.yesterdayratio = ((this.yesterday / this.daynumber)*100).toFixed(1)
      this.drawLine();
    },
    drawLine () {
      let myChart = echarts.init(document.querySelector('.main_companel'))
      myChart.setOption({
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          }
        },
        tooltip: {
          trigger: 'axis'
        },
        grid: {
          left: '0%',
          right: '5%',
          bottom: '5%',
          top: '6%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: true, 
          data: this.xData,
          axisTick:{
            lineStyle:{
              width:0 
            }
          },
          axisLine:{  
              lineStyle:{
                width:2,  
                color:'#33a4db'  
              }
          },
          axisLabel: {
              textStyle: {
                  color: '#fff'
              }
          }
        },
        yAxis: {
          type: 'value',
          axisTick:{
            lineStyle:{
              width:0 
            }
          },
          splitLine:{  
            show:true,
            lineStyle:{
              color:'rgba(51, 164, 219, 0.9)',
              width: 1,
              type:'dotted'
            }
          },
          axisLine:{  
              lineStyle:{
                width:2,  
                color:'#33a4db'  
              }
          },
          axisLabel: {
            textStyle: {
                color: '#fff'
            }
          }
        },
        series: [
          {
            name:'',
            type:'line',
            color:'#2f7edb',
            symbol: 'circle',     //设定为实心点
            symbolSize: 8,        //设定实心点的大小
            data:this.sData
          }
        ]
      });
    }
  },
  mounted() {
    clearInterval( this.time )
    this.time = setTimeout(()=>{
      this.fnData();
    }, 1000)
  }
}
</script>

<style scoped>
.l_co{
  width: 135px;
  height:430px;
  float: left;
  margin: 0 0 0 15px;
}
.l_co p:first-child{
  font-size: 16px;
  margin-top: 10px;
}
.l_co p{
  color: #33a4db;
  font-size: 14px;
  width: 85%;
  margin: 0px auto 10px;
}
.l_co p:not(:first-child){
  color: #ccc;
}
.l_co p:not(:first-child) span{
  color: #33a4db;
}
.l_co p span.holiday{
  width: 100%;
  height: 30px;
  list-style: 30px;
  display: block;
}
.main_companel{
  width:370px; 
  height:310px;
  float:left;
}
</style>



