<template>
  <div class="main">
    <div class="main_left">
      <div class="l_co_size">
        <p>
          <span>评论数</span>
          {{ plsArray | sum }}
        </p>
        <p>
          <span>好评率</span>
          {{ points }}%
        </p>
      </div>
      <h2>好评Top</h2>
      <ul>
        <li v-for="(item, index) in data" :key="index">{{ index+1 }}.{{ item.cn_name }}</li>
      </ul>
    </div>
    <div id="container1" class="main_right"></div>
    <p ref="newName"></p>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  data() {
    return {
      newName:'',             //默认区县
      data:{},
      plsArray:0,             //评论数
      points: 0,              //好评率
      zong:0,                 //总数
      dataList:[],            //默认数据格式
    }
  },
  methods: {
    fnData(){
      let arr = this.$attrs.right_bottom;
      let hash = {};
      const newArr = arr.reduceRight((item, next) => {
        this.zong+=Number(next.total)
        if( next.district_name =='西湖区' ){
          hash[next.district_name] ? '' : hash[next.district_name] = true && item.unshift(next.district_name);
        }else{
          hash[next.district_name] ? '' : hash[next.district_name] = true && item.push(next.district_name);
        }
        return item
      }, []);
      
      let i=0;
      let arr1=arr;
      let timer=null;
      clearInterval(timer)
      timer=setInterval(()=>{
        this.points = 0;
        let arr1=arr;
        i++;
        if(i == newArr.length){
          i=0;
        }
        this.newName = newArr[i]
        arr1 = arr1.filter(item => item.district_name=== this.newName )
        this.data = arr1;
        this.dataList=[];
        newArr.forEach(item =>{
          if( item !='富阳区' || item !='临安区' || item !=this.newName ){
            this.$refs.newName.innerHTML = this.newName;
            this.dataList.push( {name: item, value: 0} )
          }
          if( item == '临安区' ){
            this.$refs.newName.innerHTML = '临安市';
            this.dataList.push( {name: '临安市', value: 0} )
          }
          if( item == this.newName ){
            if( item =='临安区' ){
              this.$refs.newName.innerHTML = '临安市';
              this.dataList.push( {name: '临安市', value: 100} )
            }else if( item =='富阳区' ){
              this.$refs.newName.innerHTML = '富阳市';
              this.dataList.push( {name: '富阳市', value: 100} )
            }else{
              this.dataList.push( {name: this.newName, value:100} )
            }  
          }
          if( item =='富阳区' ){
            this.$refs.newName.innerHTML = '富阳市';
            this.dataList.push( {name: '富阳市', value: 0} )
          }
        })
        arr1.forEach((obj, index)=>{
          this.points+=Number(obj.total)
          this.plsArray+=Number(obj.good)
        });
        this.points = 100-(this.points/this.zong*100).toFixed(2);

        this.fnEcharts(this.dataList);
        //console.log( this.$refs.newName.innerHTML )
      }, 5000)

      this.newName = newArr[i]
      arr1 = arr1.filter(item => item.district_name=== this.newName )
      this.data = arr1;

      newArr.forEach(item =>{
        if( item =='富阳区' ){
          this.$refs.newName.innerHTML = '富阳市';
          this.dataList.push( {name: '富阳市', value: 0} )
        }
        if( item =='临安区' ){
          this.$refs.newName.innerHTML = '临安市';
          this.dataList.push( {name: '临安市', value: 0} )
        }
        if( item ==this.newName ){
          this.dataList.push( {name: this.newName, value:100} )
        }
        if( item !='富阳区' && item !='临安区' && item !=this.newName ){
          this.$refs.newName.innerHTML = this.newName;
          this.dataList.push( {name: item, value: 0} )
        }
      })

      arr1.forEach((obj, index)=>{
        this.points+=Number(obj.total)
        this.plsArray+=Number(obj.good)
      });
      this.points = 100-(this.points/this.zong*100).toFixed(2)
      this.fnEcharts(this.dataList);
    },
    fnEcharts (data) {
      let geoJson = this.$attrs;
      var mapList = [
          {
              "id": "33",
              "name": "浙江省"
          }
      ];
      function getProId(name) {
          for (let i in mapList) {
              if (mapList[i].name == name) {
                  return mapList[i].id;
              }
          }
      } 
      var myChart = echarts.init(document.getElementById("container1"));
      echarts.registerMap(getProId(mapList[0].name), geoJson);
      myChart.setOption({
        visualMap: {
            show:false, 
            min: 0,
            max: 10,
            text: ['High', 'Low'],
            seriesIndex: [1],                   //必须设置此项,否则会覆盖标注点颜色
            realtime: true,
            calculable: true,
            inRange: {
                color: ['#161d26', '#2eb9c3']
            },
            textStyle: {
                color: 'lime'
            }
        },
        geo: {
            show: true,
            map: mapList[0].id,
            label: {
                normal: {
                    show: true,
                    textStyle: {
                        color: 'rgba(255,255,255,0)'                        //省份字体颜色
                    }
                },
                emphasis: {
                    textStyle: {
                        color: 'rgba(255,255,255,0)'                        //省份字体颜色
                    },
                    label: {
                        show: false
                    }
                }
            },
            roam: true,
            itemStyle: {
                normal: {
                    show: false,
                    areaColor: 'rgba(255,255,255,0)',
                    borderColor: '#aed2ff',
                },
                emphasis: {
                    areaColor: '#161d26',
                    label: {
                        show: false
                    }
                }
            }
        },
        series: [
            {
                name: 'credit_pm2.5',
                type: 'scatter',
                coordinateSystem: 'geo',
                roam: false,
                data: []
            },
            {
                type: 'map',
                roam: false,
                map: mapList[0].id,
                geoIndex: 0,
                aspectScale: 2, //长宽比
                showLegendSymbol: false, // 存在legend时显示
                label: {
                    normal: {
                        show: false
                    },
                    emphasis: {
                        label: {
                            show: false
                        },
                        textStyle: {
                            color: '#fff'
                        }
                    }
                },
                roam: true,
                itemStyle: {
                    normal: {
                        show: false,
                        areaColor: '#031525',
                        borderColor: '#3B5077',
                    },
                    emphasis: {
                        label: {
                            show: false
                        }
                    }
                },
                animation: false,
                data: data
            }
        ]
      });
    }
  },
  mounted() {
    setTimeout(()=> {
      this.fnData();
    }, 1000)
  }
}
</script>

<style scoped>
.main{
  width: 98%;
  height:310px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
.main > p{
  position: absolute;
  right: 10px;
  bottom:10px;
  color: #fff;
  font-size: 16px;
}
.main_left{
  width: 40%;
  height: 100%;
  float: left;
}
.l_co_size{
  width: 100%;
  height: 45px;
  margin:15px 0 10px;
}
.l_co_size p{
  font-size:18px;
  width: 65%;
  float: left;
  text-align: center;
  color: #33a4db;
}
.l_co_size p:first-child{
  width: 35%;
}
.l_co_size p span{
  display: block;
  font-size: 24px;
  height: 25px;
  color: #aed2ff;
}
.main_left h2{
  color: #aed2ff;
  font-size: 16px;
  margin: 15px 0 0 20px;
}
.main_left ul{
  margin: 0 0 0 20px;
}
.main_left ul li{
  height: auto;
  line-height:24px;
  font-size: 14px;
  padding: 5px 0;
  color: #aed2ff;
}
.r_co{
  width: 50%;
  height:300px;
  float:left;
  margin: 0px 0 0;
}
.main_right{
  width:60%;
  height: 160%;
  float: right;
  margin: -13% 0 0;
}
</style>



