<template>
  <div>
    <div class="right_main">
      <div class="right_main_tab">
        <div class="alarm_set_tab">
          <to-input 
            @sInput="getChildCon"
            input-size="非法字符" 
            input-placeholder="请输入非法字符"
          ></to-input>
        </div>
        <div class="right_main_tab1">
          <div class="right_main_tab1_top">
            <h2>非法字符</h2>
          </div>
          <div class="div1_top" :style="'height:'+ this.fullHeight">
            <table cellspacing="0" cellpadding="0" border="0">
              <colgroup valign="middle">
                <col width="2%">
                <col width="68%">
                <col width="6%">
                <col width="12%">
                <col width="12%">
              </colgroup>
              <thead>
                <tr>
                  <th>NO.</th>
                  <th>字符内容</th>
                  <th>状态</th>
                  <th>创建时间</th>
                  <th>创建者</th>
                </tr>
              </thead>
              <tbody v-if="filteredUsers.length" >
                <tr v-for="(item, index) in filteredUsers" :key="index">
                  <td>{{ index+1 }}</td>
                  <td>{{ item.text }}</td>
                  <td>{{ item.code }}</td>
                  <td>{{ item.time }}</td>
                  <td>{{ item.creator }}</td>
                </tr>
              </tbody>
              <tbody v-else>
                <tr>
                  <td colspan="5" style="color:#f00;text-align:center;">暂无数据</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import ToInput from '../components/ToInput'
export default {
  name: 'mmsSource',
  components: {
    ToInput
  },
  data () {
    return {
      mmsSource: [],
      fKey: '',        
      text: '',
      fullHeight: document.documentElement.clientHeight - 210 + 'px',
    }
  },
  watch: {
    mmsSource: {
      handler () {
        this.mmsSource
      },
      deep: true
    }
  },
  computed: {
    filteredUsers () {
      let fUsers = this.queryFilter('text', this.fKey, this.mmsSource)
      return fUsers
    }
  },
  methods: {
    fnData(){
      this.mmsSource = this.$attrs.data.mmsSource;
    },
    getChildCon:function(str){
      this.fKey=str;
    },
    queryFilter (prop, key, arr) {
      if (!key) {
        return arr
      }
      arr = arr.filter((progress) => {
        if (progress[prop].toString().indexOf(key) !== -1) {
          return true
        }
      })
      if (key !== this[prop]) {
        this.currentPage = 0
        this[prop] = key
      }
      return arr
    }
  },
  mounted () {
    this.fnData();
  }
}
</script>
<style scoped>
.div1_top{
  width:100%;
  overflow-y:auto;
}
.alarm_set_tab{
    width: 100%;
    height: 30px;
    margin:0 0px 15px 0;
}
</style>



