<template>
  <div class="index_top" v-on:mouseover="mousePos()">
    <components-header 
      v-show="isShow"
    ></components-header>
    <div id="centerArea"><swiper></swiper></div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import $ from 'jquery'
import ComponentsHeader from '../components/Header'
import Swiper from '../components/swiper'
export default {
  data (){
    return{
      isShow: false
    }
  },
  components: {
    ComponentsHeader,
    Swiper
  },
  methods:{
    backHandle(){
        this.$router.back()
    },
    mousePos(e){
      e=e||window.event;
      var scrollY=document.documentElement.scrollTop||document.body.scrollTop;
      var y=e.pageY||(e.clientY+scrollY);
      if(y<=20){
        this.isShow = true
      }else{
        this.isShow=false
      }
    }
  }
}
$(function () {
  function _width () {
    let _w = $(window).width(),
        _h = $(window).height(),
        _P = (_w / 1920).toFixed(2),
        _P1 = (_h / 1080).toFixed(2);
    $("#centerArea").css({
      'width': '1920px',
      'height': '1080px',
      'transform': 'scale( ' + _P + ',' + _P1 + ' )'
    });
  }
  setInterval(function (){
    _width()
  }, 20)
})
</script>

<style scoped>
.index_top{
  margin-top: -540px;
  margin-left: -960px;
  overflow: hidden;
  position: fixed;
  top: 50%;
  left: 50%;
  background-size: 100% 100%;
}
.header1{
  width:100%;height:10px;
  background:rgba(255,255,255,0);
  position:absolute;
  left:0;
  top:0px;
  z-index: 1;
}
#centerArea{
  overflow: hidden;
}
</style>



