<template>
  <div id="page1"></div>
</template>

<script>
import $ from 'jquery'
export default {
  
}
$(function () {
  setInterval(function (){
    var _w = $(window).width()
    var _h = $(window).height()
    $('#page1').css({
      'width':  _w - 231 + 'px',
      'height': _h - 73+'px'
    })
  }, 500);
})
</script>
<style>
#page1{
  margin: 0 0 0 231px;
  background:url(../assets/img/welcome1.jpg) no-repeat 0 40px;
  background-size: 100% 100%;
}
</style>



