<template>
  <div class="index_top" v-on:mouseover="mousePos()">
    <components-header 
      v-show="isShow"
    ></components-header>
    <div id="divZ">
      <div class="main_top">
        <div class="b_left">
          <title-list size="杭州境外游客来源"></title-list>
          <div class="b_left_fbd">
            <box-list :country="country" iW="200"></box-list>
            <gwmap :country="country" />
          </div>
        </div>

        <div class="b_left">
          <title-list size="搜索分析"></title-list>
          <line-echarts :lineDay="lineDay"></line-echarts>
        </div>

        <div class="b_left">
          <title-list size="区县游客人数"></title-list>
          <b-echarts
            :right_bottom="right_bottom"
          ></b-echarts>
        </div>
      </div>

      <div class="main_bot">
        <div id="mapMove" class="t_center">
          <v-map :county="county"></v-map>
          <hz-map :county="county"></hz-map>
        </div>

        <div class="b_left">
          <title-list size="主要景区监测"></title-list>
          <v-line 
            :features="features"
            :right_bottom="right_bottom"
          ></v-line>
        </div>
      </div>

      <div class="main_top">
        <div class="b_left">
          <title-list size="国内分布地"></title-list>
          <div class="b_left_fbd">
            <box-list 
              :topTenPro="topTenPro"
              iW="300"
            ></box-list>
            <center-move :topTenPro="topTenPro" iW="370" LR="right"></center-move>
          </div>
        </div>

        <div class="b_left">
          <title-list size="景区好评率"></title-list>
          <scenic-spot-rate :right_bottom="right_bottom"></scenic-spot-rate>
        </div>

        <div class="b_left">
          <title-list size="评价分析"></title-list>
          <companel
            :lineqdhyd="lineqdhyd"
            :linexhyd="linexhyd"
          ></companel>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import axios from 'axios'
import ComponentsHeader from '../components/Header'
import VMap from '../components/Vmap'
import HzMap from '../components/hzmap'
import titleList from '../module/titleList'
import scenicSpotRate from '../module/scenicSpotRate'
import CenterMove from '../components/Centermove'
import boxList from '../module/boxList'
import gwmap from '../module/gwmap'
import VLine from '../components/Vline'
import lineEcharts from '../module/lineEcharts'
import bEcharts from '../module/bEcharts'
import companel from '../components/companel'

export default {
  name: 'HelloWorld',
  components: {
    ComponentsHeader,
    VMap,
    HzMap,
    titleList,
    scenicSpotRate,
    CenterMove,
    VLine,
    boxList,
    gwmap,
    lineEcharts,
    bEcharts,
    companel
  },
  data () {
    return {
      isShow: false,
      country:'',
      lineDay:'',
      right_bottom:'',
      topTenPro: '',
      lineqdhyd:'',
      linexhyd:'',
      county:'',
      features:''
    }
  },
  methods:{
    getSource () {
      axios.get('https://zhuy2018.github.io/vue/static/mock/home.json')
        .then(this.getVipSour)
    },
    getVipSour (res) {
      res = res.data
      if(res.ret && res.data){
        this.features = res.data.features;              //杭州13区县坐标点
        this.county = res.data.county;                  //杭州实时游客
        this.country = res.data.topTenCountry;          //杭州境外游客来源
        this.lineDay = res.data.day;                    //搜索分析
        this.right_bottom = res.data.right_bottom;      //景区好评率、评价分析
        this.topTenPro = res.data.topTenPro;            //国内分布地
        this.lineqdhyd = res.data.lineqdhyd;            //千岛湖-主要景区监测
        this.linexhyd = res.data.linexhyd;              //西湖-主要景区监测
      }
    },
    mousePos(e){
      e=e||window.event;
      var scrollY=document.documentElement.scrollTop||document.body.scrollTop;
      var y=e.pageY||(e.clientY+scrollY);
      if(y<=20){
        this.isShow = true
      }else{
        this.isShow=false
      }
    }
  },
  created(){
    this.getSource()
  }
}
$(function () {
  function _width () {
    let _w = $(window).width(),
        _h = $(window).height(),
        _P = (_w / 1920).toFixed(2),
        _P1 = (_h / 1080).toFixed(2);
    $("#divZ").css({
      'width': '1900px',
      'height': '1060px',
      'transform': 'scale( ' + _P + ',' + _P1 + ' )'
    });
  }
  setInterval(function (){
    _width()
  }, 20)
})
</script>

<style scoped>
.header1{
  width:100%;height:10px;
  background:rgba(255,255,255,0);
  position:absolute;
  left:0;
  top:0px;
  z-index: 1;
}
#divZ{
  display: flex;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;
  padding: 10px;
}
.index_top{
  margin-top: -540px;
  margin-left: -960px;
  overflow: hidden;
  position: fixed;
  top: 50%;
  left: 50%;
  background:url(../assets/img/bg.jpg) no-repeat;
  background-size: 100% 100%;
}
.main_top{
  height: 100%;
  overflow: hidden;
  flex:2;
}
.main_bot {
  width: auto;
  height: 100%; 
  flex: 3;
  overflow: hidden;
}
.b_left{
  width: 98%;
  height: 344px;
  background:url(../assets/img/top_bg.png) no-repeat;
  background-size: 100% 100%;
  margin:10px auto 0;
  color: #fff;
}
.main_top .b_left{
  margin:0 auto 15px;
  height: 343px;
}
.main_top .b_left:last-child{
  margin-bottom: 0;
}
.t_center {
  width: 98%;
  height: 700px;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  padding: 0 16px;
  margin: 0 auto 15px;
  background-size: 100% 100%;
  overflow: hidden;
}
.title{
  height: 58px;
  text-align: center;
  line-height: 58px;
  font-size: 28px;
  color: #fff;
}
#mapMove{
  position: relative;
}
.b_left_fbd{
  position: relative;
  height: 300px;
  width: 100%;
  display: inline-block;
}
</style>



