<template>
  <div>
    <div class="right_main">
      <div class="right_main_tab">
        <div class="alarm_set_tab">
          <to-select :selectList="selectList1" area="运营商" @tSelect="getSelect"></to-select>
          <to-select :selectList="selectList" area="地区" @tSelect="getSelect1"></to-select>
        </div>
        <div class="right_main_tab1">
          <div class="right_main_tab1_top">
            <h2>省内号段</h2>
          </div>
          <div class="tab_overflow" :style="'max-height:'+ Height">
            <table cellspacing="0" cellpadding="0" border="0">
              <thead>
                <tr>
                  <th>运营商名称</th>
                  <th>号段</th>
                  <th>地区</th>
                  <th>状态</th>
                  <th>创建时间</th>
                </tr>
              </thead>
              <tbody v-if="filteredUsers.length">
                <tr v-for="(item, index) in filteredUsers" :key="index">
                  <td>{{ item.name }}</td>
                  <td>{{ item.roughly }}</td>
                  <td>{{ item.region }}</td>
                  <td>{{ arrList() }}</td>
                  <td>{{ 1531901536000 *  Math.floor(Math.random() * 100) | formatDate }}</td>
                </tr>
              </tbody>
              <tbody v-else>
                <tr>
                  <td colspan="5" style="color:#f00;text-align:center;">暂无数据</td>
                </tr>
              </tbody>
            </table>
          </diV>
          <div class="ht_page">
            <ul v-show="ht_page">
              <li>
                <input 
                  type="number"
                  min="1"
                  :max="totalPage" 
                  v-model="jPage"
                  style="width:50px;text-align:center;float:left" 
                  placeholder="页码" 
                />
              </li>
              <li @click="jumpToPage"><a>跳转</a></li>
              <li @click="first()"><a>首页</a></li>
              <li @click="turnPage1(1)"><a>上页</a></li>
              <li><a><font color="#00aeff">{{ currentPage+1 }}</font></a></li>
              <li @click="turnPage(1)"><a>下页</a></li>
              <li @click="back()"><a>尾页</a></li>
              <li><span>共 {{ totalPage }} 页</span></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import ToSelect from '../components/ToSelect'
export default {
  name: 'channel',
  components: {
    ToSelect
  },
  props: {
    routerHeight: ''
  },
  data () {
    return {
      Arr: ["启用", "停用"],
      channel: [],
      selectList:[
        { text: '所有', value: '' },
        { text: '衢州市本级', value: '衢州' },
        { text: '杭州市本级', value: '杭州' },
        { text: '湖州市本级', value: '湖州' },
        { text: '嘉兴市本级', value: '嘉兴' },
        { text: '宁波市本级', value: '宁波' },
        { text: '绍兴市本级', value: '绍兴' },
        { text: '台州市本级', value: '台州' },
        { text: '温州市本级', value: '温州' },
        { text: '丽水市本级', value: '丽水' },
        { text: '金华市本级', value: '金华' },
        { text: '舟山市本级', value: '舟山' }
      ],
      Height: parseInt(this.routerHeight) - 170 +'px',
      selectList1:[
        { text: '所有', value: '' },
        { text: '中国移动', value: '中国移动' },
        { text: '中国联通', value: '中国联通' },
        { text: '中国电信', value: '中国电信' }
      ],
      aKey: '',             // select audit 的关键字
      name: '',             // 上一次过滤的 audit关键字，初始化为''
      aKey1: '',             // select audit 的关键字
      region: '',             // 上一次过滤的 audit关键字，初始化为''
      limit: 15,            // 每页显示行数
      totalPage: 0,         // 总页数
      currentPage: 0,       // 当前页
      jPage: 1,             // 跳转到某页
      ht_page: true         // 数据是否有显示分页
    }
  },
  watch: {
    channel: {
      handler () {
        this.channel
      },
      deep: true
    }
  },
  filters: {
    formatDate: function (value) {
      let date = new Date(value);
      let y = Math.floor(Math.random()*(2019-2016+1)+2016);
      let MM = date.getMonth() + 1;
          MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
      let m = date.getMinutes();
          m = m < 10 ? ('0' + m) : m;
      let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    }
  },
  computed: {
    filteredUsers () {
      let fUsers = this.queryFilter('', this.fKey, this.channel)
          fUsers = this.queryFilter('name', this.aKey, fUsers)
          fUsers = this.queryFilter('region', this.aKey1, fUsers)
      
      if( fUsers.length < this.limit || fUsers.length === this.limit ){
        this.ht_page = false
      }else{
        this.ht_page = true
      }
      return this.paginate(fUsers)
    }
  },
  methods: {
    fnData(){
      this.channel = this.$attrs.data.channel
    },
    arrList() {
      return this.Arr[Math.floor(Math.random() * this.Arr.length + 1)-1]
    },
    getSelect: function (str){
      this.aKey = str;
    },
    getSelect1: function (str){
      this.aKey1 = str;
    },
    queryFilter (prop, key, arr) {
      if (!key) {
        return arr
      }
      arr = arr.filter((progress) => {
        if (progress[prop].toString().indexOf(key) !== -1) {
          return true
        }
      })
      if (key !== this[prop]) {
        this.currentPage = 0
        this[prop] = key
      }
      return arr
    },
    paginate (arr) {
      // totalPage
      this.totalPage = Math.ceil(arr.length / this.limit)
      let page = this.currentPage
      let curLimit = this.limit
      // 返回指定条数的数组
      arr = arr.slice(curLimit * page, curLimit * (page + 1))
      return arr
    },
    first () {
      this.currentPage = 0
    },
    back () {
      this.currentPage = this.totalPage - 1
    },
    turnPage (num) {
      if (num === 1) {
        if (this.currentPage === this.totalPage-1) {
          return
        } else {
          this.currentPage++
        }
      } else {
        if (this.currentPage === 0) {
          return
        } else {
          this.currentPage--          
        }
      }
    },
    turnPage1 (num) {
      if (num === 1) {
        if (this.currentPage === 0) {
          return
        } else {
          this.currentPage--
        }
      } else {
        if (this.currentPage === 0) {
          return
        } else {
          this.currentPage--          
        }
      }
    },
    jumpToPage () {
      let jPage = this.jPage - 1
      this.currentPage = jPage
    }
  },
  mounted () {
    this.fnData();
    this.arrList()
  }
}
</script>

<style scoped>
.alarm_set_tab{
  width: 100%;
  height: 30px;
  margin:0 0px 15px 0;
}
.alarm_set_tab a{
  width:96px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  display: block;
  background:url(../assets/img/alarm_set_tab.png) no-repeat;
  color: #792a04;
  font-size:15px;
  font-family:"微软雅黑";
  float:left;
  margin-left: 10px;
}
.alarm_set_tab a:hover{
    opacity:0.8;filter:alpha(opacity=80);-moz-opacity:0.8;-khtml-opacity:0.8;
}

.ht_page {
    width: 100%;
    height: 39px;
}
.ht_page ul{
    float: right;
}
.ht_page ul > li {
    display: inline;
    line-height: 39px;
}
.ht_page ul > li:not(:last-child){
    cursor: pointer;
}
.ht_page ul > lia,.ht_page ul > li>a {
    color: #63666d;
    padding: 4px 6px;
    font-family: Arial,Verdana,"微软雅黑";
    font-size: 14px;
}
.ht_page ul > li>a:hover{color: #0fb1d6;text-decoration: underline;}
.ht_page ul > li>span {
    float: left;
    padding: 4px 6px;
    line-height: 20px;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid #ddd;
    border-left-width: 0;
    color: #aaa;
}
.ht_page ul li:last-child{
    float:right;
    margin:5px;
}
.ht_page ul li input{
    margin:5px 0;
    display: inline-block;
    height: 20px;
    padding: 4px;
    font-size: 14px;
    line-height:20px;
    border: 1px solid #ccc;
    width:50px;
    text-align:center;
}
.ht_page ul li font{
  color:#00aeff;
}
</style>



