<template>
  <div id="myChart1"></div>
</template>

<script>
import echarts from 'echarts'
import { getBeforeDate } from '@/js/filters'
export default {
  props: {
    lineDay: {}
  },
  name: 'line-echarts',
  data(){
    return {
      day:[],
      data:[],
      dataMax:[]
    }
  },
  watch: {
    lineDay (val, oldVal){
      for(let i=0;i<val.length;i++){
        if( Number(val[i].value)  == 0 ){
          this.data.unshift( 460647 )
        }else{
          this.data.unshift( val[i].value )
        }
      }
      for(let i=0;i<12;i++){
        this.day.unshift( getBeforeDate(i) )
      }
      this.drawLine()
    }
  },
  methods: {
    drawLine () {
      let myChart = echarts.init(document.getElementById('myChart1'))
      myChart.setOption({
        grid:{
          top:'8%',
          left:'14%',
          right:'3%',
          bottom: '15%'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          }
        },
        legend: {
          show: false
        },
        color: this.color,
        calculable: true,
        xAxis: [{
          type: 'category',
          axisTick: {
            show: false
          },
          nameTextStyle: {
            color: 'rgba(255, 255, 255, 0.69)'
          },
          axisLabel: {
            textStyle: {
              color: '#b2e1ff',
              fontSize: 12
            }
          },
          axisLine:{
            lineStyle:{
              color:'#026495',
              width:2,
            }
          },
          data: this.day
        }],
        yAxis: [{
          axisLine:{
            lineStyle:{
                color:'#026495',
                width:2,
            }
          },
          nameLocation: 'end',
          nameGap: 20,
          nameRotate: 0,
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              color: ['rgba(255, 255, 255, 0.5)'],
              width: 1,
              type: 'solid'
            }
          },
          axisLabel: {
            textStyle: {
              color: '#b2e1ff',
              fontSize: 12
            }
          },
          type: 'value',
          nameTextStyle: {
            color: 'rgba(255, 255, 255, 0.69)'
          }
        }],
        series: [{
          name: '搜索分析',
          type: 'line',
          symbol:'circle',
          symbolSize:5,   
          color:['#4070ff'], 
          lineStyle: {
            normal: {
              color: '#4070ff',
              width: 2
            }
          },
          data: this.data
        }]
      });
    }
  },
  mounted(){
    
  }
}
</script>

<style scoped>
#myChart1{
  width: 510px;
  height: 300px;
  margin: 0 auto;
}
</style>



