<template>
  <div>
    <div class="right_main">
      <div class="right_main_tab">
        <div class="alarm_set_tab">
          <to-input 
            @sInput="getChildCon"
            input-size="业务名称" 
            input-placeholder="请输入业务名称"
          ></to-input>
          <to-select 
            :selectList="selectList" 
            area="所属区域" 
            @tSelect="getSelect1"
          ></to-select>
        </div>
        <div class="right_main_tab1">
          <div class="right_main_tab1_top">
            <h2>业务管理</h2>
          </div>
          <div class="tab_overflow" :style="'height:'+ Height">
            <table cellspacing="0" cellpadding="0" border="0">
              <thead>
                <tr>
                  <th>系统名称</th>
                  <th>地区</th>
                  <th>状态</th>
                  <th>进程</th>
                  <th>AppKey</th>
                  <th>短信编码</th>
                  <th>授权时间</th>
                  <th>单位联系人</th>
                  <th>联系人号码</th>
                </tr>
              </thead>
              <tbody v-if="filteredUsers.length" class="work_tab">
                <tr v-for="(item, index) of filteredUsers" :key="index">
                  <td>{{ item.name }}</td>
                  <td>{{ item.region }}</td>
                  <td>{{ item.state }}</td>
                  <td>{{ item.process }}</td>
                  <td>{{ item.appKey }}</td>
                  <td>{{ item.coding }}</td>
                  <td>{{ item.time }}</td>
                  <td>{{ item.phone1 }}</td>
                  <td>{{ item.phone2 }}</td>
                </tr>
              </tbody>
              <tbody v-else>
                <tr>
                  <td colspan="10" style="color:#f00;text-align:center;">暂无数据</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import ToSelect from '../components/ToSelect'
import ToInput from '../components/ToInput'
export default {
  components: {
    ToSelect,
    ToInput
  },
  props: {
    routerHeight: ''
  },
  name: 'work',
  data () {
    return {
      aKey: '',               //搜索的关键字
      aKey1: '',             //select筛选的关键字
      listWork1:[],
      selectList:[
        { text: '所有', value: '' },
        { text: '省本级', value: '省本级' },
        { text: '椒江区', value: '椒江区' },
        { text: '黄岩区', value: '黄岩区' },
        { text: '路桥区', value: '路桥区' },
        { text: '玉环县', value: '玉环县' },
        { text: '杭州市本级', value: '杭州市本级' },
        { text: '市本级', value: '市本级' },
      ],
      inputSize: String,
      inputPlaceholder: String,
      hotCities: Object,
      keyword: '',
      Height: parseInt(this.routerHeight) - 150 +'px'
    }
  },
  watch: {
    listWork1: {
      handler () {
        this.listWork1
      },
      deep: true
    }
  },
  computed: {
    filteredUsers () {
      let fUsers = this.queryFilter('', this.fKey, this.listWork1)
          fUsers = this.queryFilter('name', this.aKey, fUsers)
          fUsers = this.queryFilter('region', this.aKey1, fUsers)

          return fUsers
    }
  },
  methods: {
    getChildCon:function(str){
      this.aKey=str;
    },
    getSelect1: function (str){
      this.aKey1 = str;
    },
    fnData(){
      this.listWork1 =  this.$attrs.data.hotCities;
    },
    queryFilter (prop, key, arr) {
      if (!key) {
        return arr
      }
      arr = arr.filter((progress) => {
        if (progress[prop].toString().indexOf(key) !== -1) {
          return true
        }
      })
      if (key !== this[prop]) {
        this.currentPage = 0
        this[prop] = key
      }
      return arr
    }
  },
  mounted () {
    this.fnData();
  }
}
</script>
<style scoped>
.alarm_set_tab{
  width: 100%;
  height: 30px;
  margin:0 0px 15px 0;
}
.ht_page {
  width: 100%;
  height: 39px;
  border-top: 0 none;
}
.ht_page ul{
    float: right;
}
.ht_page ul > li {
    display: inline;
    line-height: 39px;
}
.ht_page ul > li:not(:last-child){
    cursor: pointer;
}
.ht_page ul > lia,.ht_page ul > li>a {
    color: #63666d;
    padding: 4px 6px;
    font-family: Arial,Verdana,"微软雅黑";
    font-size: 14px;
}
.ht_page ul > li>a:hover{color: #0fb1d6;text-decoration: underline;}
.ht_page ul > li>span {
    float: left;
    padding: 4px 6px;
    line-height: 20px;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid #ddd;
    border-left-width: 0;
    color: #aaa;
}
.ht_page ul li:last-child{
    float:right;
    margin:5px;
}
.ht_page ul li input{
    margin:5px 0;
    display: inline-block;
    height: 20px;
    padding: 4px;
    font-size: 14px;
    line-height:20px;
    border: 1px solid #ccc;
    width:50px;
    text-align:center;
}
.ht_page ul li font{
  color:#00aeff;
}
</style>



