<template>
  <div class="myChart_co">
    <p @click="fnClick()">
      {{ item }}
      <span :class="iBtn == true ? 'kailong':''" ></span>
    </p>
    <div class="myChart_co_label" v-show="iBtn">
      <label @click.stop="clickfun($event, index)" v-for="(item, index) in xzName" :key="index">
        <input :name="{item}" type="radio" />{{ item }}
      </label>
    </div>
    <ul class="six_month">
      <li v-for="(item, index) in sData" :key="index">
        <img v-if=" Math.floor((item/formatterMax).toFixed(2)*225) < 70 " 
            :style="{height: Math.floor((item/formatterMax).toFixed(2)*225)+'px'}" 
            src="../assets/img/traffic_transportation_icon3.png" />
        <img v-else
            :style="{height: Math.floor((item/formatterMax).toFixed(2)*225)+'px'}" src="../assets/img/traffic_transportation_icon1.png" />
      </li>
    </ul>
    <div id="myChart"></div>
  </div>
</template>

<script>
import echarts from 'echarts'
import { filters } from '@/js/filters'
export default {
  name: 'b_echarts',
  data(){
    return {
      iBtn:false,
      xzName:[],              //全部区县
      newName:[],             //点击选择区县
      xData:[],
      sData:[],
      index:0,
      item:'',
      formatterMax:0,
    }
  },
  methods: {
    fnClick(){
      this.iBtn = !this.iBtn;
    },
    clickfun(event, index){
      this.item = event.currentTarget.innerText;
      this.index = 0;
      this.index = index;
      this.iBtn = false;
      this.bData();
    },
    bData(){
      this.xData = [], this.sData = [];
      let arr = this.$attrs.right_bottom;
      const hash = {};
      const newArr = arr.reduceRight((item, next) => {
        this.zong+=Number(next.total)
        hash[next.district_name] ? '' : hash[next.district_name] = true && item.push(next.district_name);
        return item
      }, []);

      this.xzName = newArr
      this.item = this.newName = newArr[this.index]
      arr = arr.filter(item => item.district_name === this.newName )
      //this.data = arr;
      arr.forEach(item=>{
        this.sData.unshift( item.total )
        this.xData.unshift( item.cn_name )
      })
      this.drawLine()
    },
    drawLine () {
      let formatterMax;
      let myChart = echarts.init(document.getElementById('myChart'))
      myChart.setOption({
        tooltip : {
          backgroundColor :'#19486e',
          borderColor :'#228f96',
          borderRadius :10,
          borderWidth:1,
          textStyle:{
            align:'shadow'
          },
          trigger: 'item',
          formatter: function(params, ticket, callback) {
            if(params){
              return params.seriesName+ "<br />" +params.name+ ":" +filters(params.data)
            }
          }
        },
        grid: {
          left: '2%',
          right: '2%',
          bottom: '2%',
          top: '8%',
          containLabel: true
        },
        xAxis : [
          {
            type : 'category',
            splitLine:{show: false},
            data: this.xData,
            axisLabel:{
              formatter: function(val) {
                if(val.length>=12){
                  val=val.substring(0,8)+"...";
                }
                var strs = val.split('');             //字符串数组
                var str = ''
                for(var i = 0, s; s = strs[i++];) { //遍历字符串数组
                  str += s;
                  if(!(i % 4)) str += '\n'; //按需要求余
                }
                return str
              },
              textStyle:{
                color:"#93badb",
                fontSize :12
              }
            },
            axisLine:{
              lineStyle:{
                color:"#0BA4C0"
              }
            },
            axisTick: {
              alignWithLabel:true
            }
          }
        ],
        yAxis : [
          {
            axisLabel:{
              show:true,
              interval: 'auto', 
              //formatter: '{value}', 
              formatter: function (value, index) {
                formatterMax = value;
                return value;
              },
              textStyle:{
                color:"#93badb",
                fontSize : 14
              }
            },
            axisLine:{
              lineStyle:{
                color:"#0BA4C0"
              }
            },
            splitLine:{show: false},
            type : 'value'
          }
        ],
        series : [
            {
                name:'区县游客人数',
                label: {
                    normal: {
                      color:"rgba(255,255,255,0)",
                      show: true,
                      position: 'top'
                    }
                },
                type:'bar',
                barWidth: '30%',
                data:this.sData,
                itemStyle: {
                  color: 'rgba(23,171,224,0)'
                  // color: function(params) { 
                  //   var colorList = [ '#159CCC', '#49DDBD' ];
                  //   return colorList[params.dataIndex % 2]
                  // },
                },
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                },
                animationDelayUpdate: function (idx) {
                  return idx * 50;
                }
            }
        ]
      });
      this.formatterMax = formatterMax;
    }
  },
  mounted(){
    let self=this;
    if (self && !self._isDestroyed) {
        setTimeout(() => {
            if (self && !self._isDestroyed)
                self.bData();
        }, 600);
    }
  }
}
</script>

<style scoped>
.myChart_co{
  width: 510px;
  height: 300px;
  margin: 0 auto;
  position: relative;
}
.myChart_co p{
  width: 80px;
  height: 26px;
  line-height: 26px;
  position: absolute;
  right: -25px;
  top: -26px;
  font-size: 12px;
  color: #0a9cf9;
  cursor: pointer;
}
.myChart_co p span{
	width:0;
	height:0;
	border-right:6px solid transparent;
	border-left:6px solid transparent;
	border-top:6px solid #0a9cf9;
  vertical-align: -10px;
  margin: 0 4px;
}
.myChart_co p span.kailong{
  border-right:6px solid transparent;
  border-left:6px solid transparent;
  border-bottom:6px solid #0a9cf9;
  border-top: 0px none;
  vertical-align: 0px;
  display: inline-block;
}
#myChart{
  width: 510px;
  height: 300px;
  margin: 0 auto;
}
.myChart_co_label{
  width: 150px;
  height: 240px;
  overflow: hidden;
  overflow-y: auto;
  border: 2px solid #003d5d;
  position: absolute;
  right: -1px;
  top: -268px;
  background: #19243a;
  z-index: 1;
}
.myChart_co_label label{
  width: 80%;
  padding: 0 10%;
  height: 26px;
  line-height: 26px;
  display:block;
  margin: 10px auto;
  color: #47b2f9;
  cursor: pointer;
}
.myChart_co_label label:hover{
  background: rgba(0,61,93,0.5)
}
.myChart_co_label label:first-child{
  margin-top: 0;
}
.myChart_co_label label:last-child{
  margin:0 auto;
}
.myChart_co_label label input{
  margin:0 15px 0 0;
  vertical-align: -3px;
}
input[type=radio]:after{
  content: "";
  display:block;
  width: 16px;
  height: 16px;
  text-align: center;
  line-height: 14px;
  font-size: 14px;
  border: 2px solid #003d5d;
  background-color:#19243a;
  box-sizing:border-box;
  border-radius: 50%;
  margin: -2px;
}
input[type=radio]:checked:after{
  content: '';
  background-color: #0a9cf9;
}
ul.six_month {
  width: 445px;
  height: 225px;
  position: absolute;
  left: 54px;
  top: 25px;
}
.six_month li{
  width:30px;
  height:100%;
  background-size: 100% 100%;
  float: left;
  position: relative;
  cursor:pointer;
}
.six_month li:first-child{
  margin: 0 0 0 19px;
}
.six_month li:not(:first-child){
  margin: 0 0 0 45px;
}
.six_month li:hover{
  opacity: 0.8;
}
.six_month li img{
  width: 100%;
  bottom:0;
  position: absolute;
  height:0;
}
</style>



