<template>
  <div class="warp">
    <span ref="realtime"></span>
    <div class="number2"></div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  props:{
    county:{}
  },
  data(){
    return {
      number:'00000000',
      arr:[],
      name:[]
    }
  },
  watch:{
    county(val, oldval){
      val.forEach((item, index)=>{
        this.arr.push(item.total+', '+item.cn_name)
      }); 
      let i=0;
      let num=this.arr.length;
      setInterval(()=>{
        i++;
        if(i==num){
          i=0;
        }
        this.number = this.arr[i].split(', ')[0];
        let number = $(".number2").numberAnimate({num:PrefixInteger(this.number, 8), speed:1000});
        this.$refs.realtime.innerHTML = this.arr[i].split(', ')[1] + '<br/>实时游客'
      }, 5000)
      this.number = this.arr[0].split(', ')[0];
      this.$refs.realtime.innerHTML = this.arr[0].split(', ')[1] + '<br/>实时游客'
      function PrefixInteger(num, n) {
        return (Array(n).join(0) + num).slice(-n);
      }
      this.numberGrow();
    }
  },
  methods: {
    numberGrow () {
      function PrefixInteger(num, n) {
        return (Array(n).join(0) + num).slice(-n);
      }
      ;(function($) {
        $.fn.numberAnimate = function(setting) {
          var defaults = {
            speed : 1000,
            num : "",
            iniAnimate : true, 
            symbol : '',
            dot : 0
          }
          var setting = $.extend(defaults, setting);
          
          if($(this).length > 1){
            console.log("亲，没有数字!");
            return;
          }
        
          if(setting.num == ""){
            console.log("must set a num!");
            return;
          }
          var nHtml ='<div class="mt-number-animate-dom" data-num="{{num}}">\
                    <span class="mt-number-animate-span">0</span>\
                    <span class="mt-number-animate-span">1</span>\
                    <span class="mt-number-animate-span">2</span>\
                    <span class="mt-number-animate-span">3</span>\
                    <span class="mt-number-animate-span">4</span>\
                    <span class="mt-number-animate-span">5</span>\
                    <span class="mt-number-animate-span">6</span>\
                    <span class="mt-number-animate-span">7</span>\
                    <span class="mt-number-animate-span">8</span>\
                    <span class="mt-number-animate-span">9</span>\
                    <span class="mt-number-animate-span">,</span>\
              </div>';
        
          var numToArr = function(num){
            //num = parseFloat(num).toFixed(setting.dot);
            if(typeof(num) == 'number'){
              var arrStr = num.toString().split("");  
            }else{
              var arrStr = num.split("");
            }
            
            //console.log(arrStr);
            return arrStr;
          }
        
          var setNumDom = function(arrStr){
            var shtml = '<div class="mt-number-animate">';
            for(var i=0,len=arrStr.length; i<len; i++){
              if(i != 0 && (len-i)%3 == 0 && setting.symbol != "" && arrStr[i]!="."){
                shtml += '<div class="mt-number-animate-dot">'+setting.symbol+'</div>'+nHtml.replace("{{num}}",arrStr[i]);
              }else{
                shtml += nHtml.replace("{{num}}",arrStr[i]);
              }
            }
            shtml += '</div>';

            return shtml;
          }
        
          var runAnimate = function($parent){
            $parent.find(".mt-number-animate-dom").each(function() {
              var num = $(this).attr("data-num");
              num = (num=="."?10:num);
              var spanHei = $(this).height()/11; 
              var thisTop = -num*spanHei+"px";
              if(thisTop != $(this).css("top")){
                if(setting.iniAnimate){
                  if(!window.applicationCache){
                    $(this).animate({
                      top : thisTop
                    }, setting.speed);
                  }else{
                    $(this).css({
                      'transform':'translateY('+thisTop+')',
                      '-ms-transform':'translateY('+thisTop+')',  
                      '-moz-transform':'translateY('+thisTop+')',  
                      '-webkit-transform':'translateY('+thisTop+')', 
                      '-o-transform':'translateY('+thisTop+')',
                      '-ms-transition':setting.speed/1000+'s',
                      '-moz-transition':setting.speed/1000+'s',
                      '-webkit-transition':setting.speed/1000+'s',
                      '-o-transition':setting.speed/1000+'s',
                      'transition':setting.speed/1000+'s'
                    }); 
                  }
                }else{
                  setting.iniAnimate = true;
                  $(this).css({
                    top : thisTop
                  });
                }
              }
            });
          }
        
          var init = function($parent){
            $parent.html(setNumDom(numToArr(setting.num)));
            runAnimate($parent);
          };
          this.resetData = function(num){
            var newArr = numToArr(num);
            var $dom = $(this).find(".mt-number-animate-dom");
            if($dom.length < newArr.length){
              $(this).html(setNumDom(numToArr(num)));
            }else{
              $dom.each(function(index, el) {
                $(this).attr("data-num",newArr[index]);
              });
            }
            runAnimate($(this));
          }
          init($(this));
          return this;
        }
      })($);
      let number = $(".number2").numberAnimate({num:PrefixInteger(this.number, 8), speed:1000});
    }
  },
  mounted () {
    
  }
}
</script>

<style>
.warp{
  width: 100%; 
  height: 60px;
  line-height: 60px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  padding:10px 0;
  background: rgba(0,0,0,.3);
}
.warp > span{
  width: 12%;
  text-align: center;
  color: #fff;
  line-height: 30px;
  display: block;
  float: left;
  font-size: 18px;
}
.number2{
  float: right;
  width:88%;
  font-size: 46px;
  color: #fff;
  font-family: "Droid Sans Mono", monospace;
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  position: relative;
}
.mt-number-animate{line-height:60px;height:60px;font-size:44px;overflow:hidden;display:inline-block;position:relative;margin:0 0 0 -1px;}
.mt-number-animate .mt-number-animate-dot{width:15px;line-height:60px;float:left;text-align:center;}
.mt-number-animate .mt-number-animate-dom{margin: 0 12px 0;color:#f3ff43;width:66px;text-align:center;float:left;position:relative;top:0;}
.mt-number-animate .mt-number-animate-dom:first-child{
  margin-left: 0;
}
.mt-number-animate .mt-number-animate-dom:last-child{
  margin-right: 0;
}
.mt-number-animate .mt-number-animate-dom .mt-number-animate-span{width:100%;float:left;background-color: rgba(122, 122, 122, 1);}
</style>



