<template>
  <div>
    <div class="left_nav" :style="'height:'+ this.fullHeight">
      <components-header></components-header>
      <ul class="nav1">
        <li>
          <h2><span class="iconfont s_font_big">&#xe60a;</span> 管理配置</h2>
          <ul class="list">
            <router-link :to="{name: 'VipSource'}" exact tag="li">
              <a><span class="iconfont s_font">&#xe670;</span> 交通方式</a>
            </router-link>
            <router-link :to="{name: 'mmsSubscribers'}" tag="li">
              <a><span class="iconfont s_font">&#xe670;</span> 历史客流量</a>
            </router-link>
            <router-link :to="{name: 'Work'}" tag="li">
              <a><span class="iconfont s_font">&#xe670;</span> 业务管理</a>
            </router-link>
            <router-link :to="{name: 'Study'}" tag="li">
              <a><span class="iconfont s_font">&#xe670;</span> 连接管理</a>
            </router-link>
            <router-link :to="{name: 'mmsSource'}" tag="li">
              <a><span class="iconfont s_font">&#xe670;</span> 非法字符</a>
            </router-link>
            <router-link :to="{name: 'channel'}" tag="li">
              <a><span class="iconfont s_font">&#xe670;</span> 运营商号段</a>
            </router-link>
          </ul>
        </li>
      </ul>
    </div>
    
    <router-view class="center" 
      :routerHeight="fullHeight"
      :listData="listData"
      v-if="listData"
      :data="listData"
    ></router-view>

  </div>
</template>

<script>
import axios from 'axios'
import ComponentsHeader from '../components/Header'
export default {
  name: 'about',
  data () {
    return {
      data:{},
      listData:'',
      fullHeight: document.documentElement.clientHeight - 73 + 'px'
    }
  },
  components: {
    ComponentsHeader
  },
  watch: {
    fullHeight (val) {
      if (!this.timer) {
        this.fullHeight = val
        this.timer = true
        let that = this
        setTimeout(function () {
          //console.log(that.fullHeight)
          that.timer = false
        }, 400)
      }
    }
  },
  methods:{
    getSource () {
      axios.get('/static/mock/work.json')
        .then(this.getVipSour)
    },
    getVipSour (res) {
      res = res.data
      if(res.ret && res.data){
        this.listData = res.data
      }
    }
  },
  mounted() {
    this.getSource()
    const that = this
    window.onresize = () => {
      return (() => {
        window.fullHeight = document.documentElement.clientHeight
        that.fullHeight = window.fullHeight - 73
      })()
    }
  }
}
</script>

<style scoped>
.left_nav ul li .list li a:hover .s_font{
    color:rgb(15,177,214);
}
.left_nav ul li .list li.activeAAA a .s_font{
  color: #0fb1d6;
}
.s_font{
  color:#777;
  font-size:18px;
  margin: 0 0 0 -21px;
}
</style>



