<template>
  <div class="header">
    <div class="logo">杭州大数据旅游展示平台</div>
    <ul class="nav">
      <li>
        <!-- <router-link to='/' exact tag='div' active-class='active1'> -->
        <router-link to='/' exact>
          <span class="iconfont nav_index">&#xe61e;</span>
           首页展示
        </router-link>
      </li>
      <li>
        <router-link to='/about'>
          <span class="iconfont nav_index2">&#xe670;</span>
          管理配置
        </router-link>
      </li>
      <li>
        <router-link to='/distribution'>
          <span class="iconfont nav_index3">&#xe6d4;</span>
          数据分析
        </router-link>
      </li>
    </ul>

    <div class="header_right">
      <ul class="nav ace-nav pull-right">       
        <li class="light-blue user-profile">
          <a class="user-menu dropdown-toggle" href="javascript:;" data-toggle="dropdown">
            <span id="user_info">欢迎您！管理员</span>
             {{ this.sHour }}
            <i class="iconfont i_confont">&#xe64a;</i>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import { curDateTime } from '@/js/filters';
export default {
  name: 'Header',
  data (){
    return{
      sHour:''
    }
  },
  created(){
    window.addEventListener('scroll', this.hadeleScroll)
  },
  methods: {
    hadeleScroll() {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      console.log( scrollTop )
    }
  },
  mounted(){
    this.sHour = curDateTime()
    //console.log( curDateTime() )
  }
}
$(function () {
  function _width () {
    let _w = $(window).width(),
        _h = $(window).height(),
        _P = (_w / 1920).toFixed(2),
        _P1 = (_h / 1080).toFixed(2);
    $(".header").css({
      'transform': 'scaleX( ' + _P + ' )'
    });
  }
  setInterval(function (){
    _width()
  }, 20)
})
</script>

<style>
.header{
  width: 1920px;
  height:72px;
  background:#202020;
  z-index:2;
  position: fixed;
  top:0px;
  left:0px;
  -moz-transition:.5s;
  -webkit-transition:.5s;
  -o-transition:.5s;
  transition: .5s;
  overflow: hidden;
  transform-origin: left center;
}
.logo{
  width: 380px;
  height:72px;
  line-height: 72px;
  font-size: 24px;
  color: #898e92;
  float:left;
  margin: 0 0 0 15px;
  background-size: 58% 76%;
}
.nav{
  position:relative;
  height:72px;
  z-index:2;
  float: left;
  color: #999;
}
.nav > li{
  width: 165px;
  height: 72px;
  cursor: pointer;
  float: left;
  line-height:72px;
  font-size: 16px;
  font-family: "微软雅黑";
  text-align:center;
}
.nav > li a{
  width: 100%;
  height: 100%;
  display: block;
  color:#4c5153;
  float: left;
  font-size:14px;
}
.header_right ul li.light-blue a:hover .i_confont,
.header_right ul li.light-blue a:hover #user_info,
.nav li a.router-link-exact-active,
.router-link-exact-active span,
.nav li a:hover,.nav li a:hover span,
.nav li a.activeAAA, .nav li a.activeAAA span{
    color: #fff;
}
.right_main {
  position: relative;
  height: auto;
  padding:20px 0;
  text-align: initial;
  margin:0 23px 0 231px;
}
.right_main_tab1 {
    height: auto;
    width: 100%;
    border: 1px solid #e7e7e7;
}
.right_main_tab1:hover{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.right_main_tab1_top{
  width: 100%;
  height: 32px;
  margin:0 auto;
  border-bottom: 0 none;
  border-radius: 5px 5px 0 0;
}
.right_main_tab1_top h2{
  font-size:16px;
  color: #63666d;
  font-family:"微软雅黑";
  margin:0 0 0 25px;
  height: 32px;
  line-height: 32px;
  float: left;
  padding:10px 0;
}
.tab_overflow{
  width:100%;
  overflow-y: auto;
  border-top: 1px solid #e9e9e9;
}
::-webkit-scrollbar
{
  width: 6px;
  height: 6px;
  transition: .3s;
}
::-webkit-scrollbar-track
{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb
{
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #DFDFDF;
}

.right_main_tab1 table{
  width: 98.2%;
  margin:12px auto;
  color: #000;
}
.right_main_tab1 table thead{
  height:39px;line-height:39px;color:#434343;
}
.right_main_tab1 table thead tr th{
  text-align: initial;
  height:39px;line-height:20px;
  font-family:"微软雅黑",Arial,Verdana; 
  font-size:14px;padding: 0 4px;
  font-weight: normal;
}
.right_main_tab1 table tbody tr{
  height:43px;
}
.right_main_tab1 table tbody tr td{
  height:43px;
  padding: 0 4px;
  text-align: initial;
  font-weight:normal;font-size:13px;font-family: Arial, Helvetica, "微软雅黑";
  word-break:break-all;
  border-bottom: 1px solid #e1e1e1;
 }
.right_main_tab1 table tbody tr:last-child td{
  border:0 none;
}
.right_main_tab1 table tbody tr td em{
    font-style: normal;
    color: #00aeff;
}
.right_main_tab1 table tbody tr td span.sent{
    color: #05a924;
}
.right_main_tab1 table tbody tr td span.invalid{
    color: #e71f19;
}
.right_main_tab1 table tbody tr td:last-child{
    padding:0 5px;
}
.right_main_tab2{
    width: 100%;
}

.header_right {
  height: 72px;
  float: right;
  margin: 0 5px;
}
.header_right ul li.light-blue{
  background: none;
  border: 0 none;
  height: 72px;
  line-height: initial;
  font-size:16px;
}
.header_right ul li.light-blue a > span{
  width:auto;
  display:block;
  color: #999;
  font-family: "微软雅黑";
  margin: 18px 0 0;
}
.header_right ul li.light-blue a > span s{
    text-decoration: none;
}
.header_right ul li.light-blue a > span i {
    font-style: normal;
}
.header_right ul li a > em {
  font-size: 12px;
  font-family: Arial,Verdana,"微软雅黑";
  font-style: normal;
  text-align: center;
  color: #999;
  height: 20px;
  line-height: 20px;
  margin:6px 10px 0 30px;
  white-space: nowrap;
  float: left;
}
.i_confont{
    float: right;
    margin:-15px 6px 0 0;
    color: #4c5153;
    font-size: 14px;
}
.nav_index,.nav_index2{
  color:#4c5153;
  font-size:20px;
  vertical-align:middle;
}
.nav_index3{
  color:#4c5153;
  font-size:26px;
  vertical-align: middle;
}
</style>



