<template>
    <div id="move" :style="{width: iW+'px', float: LR}"></div>
</template>

<script>
import echarts from 'echarts'
import 'echarts/map/js/china.js'
import geoCoord from '../views/js/geoCoord.js'
export default {
  name: 'move',
  data(){
      return {
        markLineDate:[]
      }
  },
  props:{
    iW:String,
    LR:String,
    topTenPro:{}
  },
  watch: {
    topTenPro(val, oldVal){
        val.forEach((item, i)=>{
            if( item.dataName ){
                this.markLineDate.push([{name: item.dataName,value:Number(item.percent).toFixed(2)},{name:'鲁迅故里'}]);
            }else{
                this.markLineDate.push([{name: item.city,value:Number(item.percentage).toFixed(2)},{name:'杭州'}]);
            }
        })
        this.moveLine()
    }
  },
  methods: {
    moveLine () {
      var proOption;
      var proMapChar;
      var convertData = function(data) {
          var res = [];
          for (var i = 0; i < data.length; i++) {
              var dataItem = data[i];
              var fromCoord = geoCoord[dataItem[0].name];
              var toCoord = geoCoord[dataItem[1].name];
              if (fromCoord && toCoord) {
                  /*res.push([{
                      coord: fromCoord,
                      value: dataItem[0].value
                  }, {
                      coord: toCoord,
                  }]);*/
                  res.push({
                      fromName: dataItem[0].name,
                      toName: dataItem[1].name,
                      coords: [fromCoord, toCoord],
                      value: dataItem[0].value
                  });
              }
          }
          return res;
      };
      var color = ['#a6c84c', '#ffa022', '#46bee9'];
      var series = [];
      [['杭州', this.markLineDate]].forEach(function(item, i) {
        series.push(
          {
            type: 'lines',
            zoom:2,
            zlevel: 2,
                  effect: {
                      show: true,
                      period: 4,
                      trailLength: 0.02,
                      symbol: 'arrow',
                      symbolSize: 5,
                  },
                  lineStyle: {
                      normal: {
                          width: 1,
                          opacity: 0.6,
                          curveness: 0.2
                      }
                  },
                  data: convertData(item[1])
          },{
                  type: 'effectScatter',
                  zoom:2,
                  coordinateSystem: 'geo',
                  zlevel: 2,
                  rippleEffect: {
                      period: 4,
                      brushType: 'stroke',
                      scale: 4
                  },
                  label: {
                      normal: {
                          show: true,
                          position: 'right',
                          offset: [5, 0],
                          formatter: '{b}'
                      },
                      emphasis: {
                          show: true
                      }
                  },
                  symbol: 'emptyCircle',
                  symbolSize: function(val) {
                      return 5;
                  },
                  itemStyle: {
                      normal: {
                          show: false,
                          color: '#f00'
                      }
                  },
                  data: item[1].map(function(dataItem) {
                      return {
                          name: dataItem[0].name,
                          value: ((geoCoord[dataItem[0].name] == undefined) ? dataItem[0].value : geoCoord[dataItem[0].name].concat([dataItem[0].value]))
                      };
                  }),
              },{
                  type: 'scatter',
                  zoom:2,
                  coordinateSystem: 'geo',
                  zlevel: 2,
                  rippleEffect: {
                      period: 4,
                      brushType: 'stroke',
                      scale: 4
                  },
                  label: {
                      normal: {
                          show: true,
                          position: 'right',
                          color: '#00ffff',
                          formatter: '{b}',
                          textStyle: {
                              color: "#00ffff"
                          }
                      },
                      emphasis: {
                          show: true
                      }
                  },
                  symbol: 'emptyCircle',
                  symbolSize: function(val) {
                      return 5;
                  },
                  itemStyle: {
                      normal: {
                          show: true,
                          color: '#f5f5f5'
                      }
                  },
                  data: [{
                      name: item[0],
                      value: geoCoord[item[0]].concat([100]),
                  }],
              }
            );
      });
      proOption = {
        grid: {
          top:'-20px',
          bottom: '-20px',
        },
        color: ['gold','aqua','lime'],
        tooltip : {
          trigger: 'item',
          backgroundColor : 'rgba(38,90,68,0.9)',
            formatter:function(params, ticket, callback){
                if(params.seriesType=="effectScatter") {
                    return params.data.name+""+params.data.value[2]+"%";
                }else if(params.seriesType=="lines"){
                    return params.data.fromName+">"+params.data.toName+"<br />"+params.data.value+"%";
                }else{
                    return params.name;
                }
            }
        },
        visualMap : {
          min : 0,
          max : 100,
          show:false,
          color : [ '#ff3333', 'orange', 'yellow', 'lime', 'aqua' ],
          textStyle : {
            color : '#fff'
          }
        },
        geo : {
          map : 'china',
          label : {
            emphasis: {
                show: true,
                color:'#fff',
                fontWeight: 'bold',
                backgroundColor : 'rgba(38,90,68,0.9)',
              }
          },
          roam : 'scale',
          layoutCenter : [ '50%', '50%' ],
          layoutSize : "120%",
          itemStyle : {
            normal : {
                color: '#fff',
                areaColor: '#33547F',
                borderWidth:2.5,
                borderColor:'#2EA7CE',
            },
            emphasis : {
              color : '#33547F'
            }
          }
        },
        series : series
      };
      proMapChar = echarts.init(document.getElementById('move'));
      proMapChar.setOption(proOption);

    }
  },
  mounted () {
  }
}
</script>

<style scoped>
#move{
    height: 100%;
    float: right;
    margin: 0 10px 0 0;
}
</style>



