<template>
  <table cellspacing="0" cellpadding="0" border="0">
    <thead>
      <tr>
        <th v-for="(col, index) in columns" @click="order(col)" :key="index">
          {{col}}
          <span class="arrow" :class="setOrder[col] > 0 ? 'asc':'dsc'"></span>
        </th>
      </tr>
    </thead>
    <tbody v-if="filterData.length">
      <tr v-for="(row, index) in filterData" :key="index">
        <td v-for="(col, index) in columns" :key="index">{{row[col]}}</td>
      </tr>
    </tbody>
    <tbody v-else>
      <tr>
        <td colspan="8" style="color:#f00;text-align:center;">暂无数据</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: 'ViewsHeader',
  props: {
    rows: Array,
    columns: Array,
    filterKey: String
  },
  data: function() {
    var colOrders = {}
    this.columns.forEach(function(col) {
      colOrders[col] = 1
    })
    return {
      setOrder: colOrders,
      sortKey: ''
    }
  },
  computed: {
    filterData: function() {
      var sortKey = this.sortKey
      var data = this.rows.slice()
      var filterKey = this.filterKey && this.filterKey.toLowerCase()
      var order = this.setOrder[sortKey]
      if (filterKey) {
        data = data.filter(function(row) {
          return Object.keys(row).some(function(key) {
            return String(row[key]).toLowerCase().indexOf(filterKey) > -1
          })
        })
      }
      if (sortKey) {
        data.sort(function(a, b) {
          a = a[sortKey]
          b = b[sortKey]
          return order > 0 ? a > b : a < b
        })
      }
      return data
    }
  },
  methods: {
    order: function(key) {
      this.sortKey = key
      this.setOrder[key] = this.setOrder[key] * -1
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
.arrow {
  margin-left: 3px;
  vertical-align: middle;
  width: 0;
  height: 0;
  cursor: pointer;
}
.arrow.dsc {
  display: inline-block;
  border: 4px solid #ccc;
  border-bottom: 0;
  /*若不为0，改变箭头方向时，也会改变箭头的位置*/
  border-color: #ccc transparent transparent;
}
.arrow.asc {
  display: inline-block;
  border: 4px solid #ccc;
  border-top: 0;
  border-color: transparent transparent #ccc;
}
</style>


